<template>
  <div>
    

	<div class="top"></div>
     <div class="nav-fixed">
          <div class="nav">
          	<a @click="$router.back(-1)" class="iconfont icon-fanhui"></a>
          		<div>服务需求</div>
          		<a></a> 
          </div>
     </div>


     <div class="accident-list">

       <div class="details-box" style="margin-top: 2.5rem;">
      	<ul>
          <!-- <li><span><em class="red">*</em> 服务区域</span><span @click='isShowa=false' class="selecttwo" style="margin-left: 0.875rem;display:inline-block;overflow: hidden;"><span class="select2-val" style='display:inline-block;overflow: hidden;'>{{ formV.serviceArea }}</span><a>▼</a></span></li> -->
          <li><span><em class="red">*</em> 服务区域</span><span class="select2-val" style='display:inline-block;overflow: hidden;'>{{ formV.serviceArea }}</span></li>
 
      		<li><span><em class="red">*</em> 联系人</span> <input maxlength="11"   v-model.trim='formV.repairMan' type="" name="" placeholder="请输入联系人"></li>
      		<li><span><em class="red">*</em> 联系号码</span> <input maxlength="11"  v-model.trim='formV.repairService' type="" name="" placeholder="请输入联系号码"></li>
          <li><span><em class="red">*</em> 服务类别</span> <span @click='isShow=false' class="selecttwo"><span class="select2-val">{{ formV.accidentType }}</span><a>▼</a></span></li>
          <div class="eventtyp-val"  v-show="isShowma">
          <li>
            <div class="eventtyp-class" v-for='vass in categorys'>
              <label @click='aa(vass)'><input type="radio" checked="true" name="eventtype-val">{{vass}}</label>
          </div>
          </li>
           </div>
           <div class="haocai" v-show="isShowhc" >
              <li><span ><em class="red">*</em> 耗材型号</span> <input  v-model.trim='formV.materialModel' type="" name="" placeholder="请输入型号"></li>
              <li><span ><em class="red">*</em> 耗材数量</span> <input v-model.trim='formV.materialNumber'type="" name="" placeholder="请输入数量"></li>
          </div>
      		<li><span><em class="red" style="visibility: hidden;">*</em> 需求描述</span><textarea v-model='formV.remark' style="text-align: right;" maxlength="100" type="" name="" placeholder="请输入需求描述"></textarea></li>
      	</ul>

 </div>


 <!-- 	报修申请成功 -->
 		<div class="acceptance" v-show="isShowbx">
 			<div class="acceptance-order-ot">
 				<div class="call"> 
 				<h1>报修申请成功</h1>
        <router-link to='/customer/index'>
 				<div class="category-order-box">
 				<div class="acceptance-order-btn">
 					<span class="problem-btn01">确定</span>
 				</div>
				</div>
      </router-link>
 				</div>
 			</div>
 		</div>
 		 <!-- 	报修申请成功 -->
     <div class="footer-none"></div>
 		<div class="bottom">
 			<div class="repair-btn" @click='subMit()'>需求提交</div>
 		</div>
 </div>
 		<div class="category-one" :class='{"display-none": isShow}'>
 			<div class="category-11">
 			<ul v-for='vas in category'>		
       <label> <li @click='ac(vas)'>{{vas}}<input type="radio" name="category" checked="true"></li></label>
      </ul>
      </div>
    </div>
    <div class="category-one" :class='{"display-none": isShowa}'>
      <div class="category-1">
      <ul v-for='va in quyu'>   
       <label> <li @click='ab(va)'>{{ va }}<input type="radio" name="isShowa" checked="true"></li></label>
       
      </ul>
      </div>
    </div>

  </div>
</template>

<script>
  import {customerNoMachCodeOrder,getUserAccount} from '../../../axiosConfig/axiosUtil'
  export default {
      name: 'Service',  

      data () {
         return {

            formV: {
              serviceArea: '',
              machCode: '' ,
              repairMan: '' ,
              repairService: '' ,
              faultType: '' ,
              accidentType: '' ,
              remark: '' ,
              materialModel: '' ,
              materialNumber: '' ,
              macdType: '移动' ,
            },
            isShow: true,
            isShowa: true,
            isShowhc: false,
            isShowma: false,
            isShowbx: false,
            quyu: ['宝安分公司','北京分公司','东莞市迅维办公设备有限公司','佛山市赛盟办公设备有限公司','深圳分公司','行业客户部','广州乐派数码科技有限公司','番禺分公司','佛山市顺德区迅维办公设备有限公司','上海分公司','苏州分公司','系统推进部','中山市迅维信息技术有限公司'],
            // category: ['保养','送货','抄表','送文件','耗材补充','设备变动','安装实施','客户培训','其他'],
                     category: ['耗材补充','设备变动','安装实施','客户培训'],
            categorys: ['加机','变更','撤除','移动']
    }
  },
     methods:{     
    customerindex: function () {
      this.$router.push({path:'/customer/customerindex'})
    },
    repairIndex: function () {
      this.$router.push({path:'/customer/repairIndex'})
    },
    feedbackindex:function(){
      this.$router.push({path:'/customer/feedbackindex'})
    },
    faultPlan:function(){
      this.$router.push({path:'/customer/faultPlan'})
    },
    accident:function(){
      this.$router.push({path:'/customer/accident'})
    },
    ac(str){
      var that = this
      that.formV.accidentType = str
      that.isShow = true
      if(str=='耗材补充'){
         this.isShowhc = true;
      }else{
         this.isShowhc = false;
      }
      if(str=='设备变动'){
         this.isShowma = true;
      }else{
         this.isShowma = false;
      } 
    },
     ab(str){ 
      var that = this
      that.formV.serviceArea = str
      that.isShowa = true
    },
    aa(str){
       var that = this
        that.formV.macdType = str
    },
    subMit(){

      var that = this;
 
        //  console.log(that.formV)
      customerNoMachCodeOrder(that.formV)
      .then((res) => {
//          if(!(/^1[34578]\d{9}$/.test(that.formV.repairService))){ 
//          that.$layerMsg("手机号码有误，请重填");  
//         return false; 
//     }else{
        // console.log(res)
      if(res.data.code == 1){
         that.$layerMsg(res.data.msg)
       }else{
        that.$layerMsg("报修成功")
        that.$router.push({path:'/customer/index'})
       }
        // console.log(res)
         // }
      })
     
    }

  }, 
   created(){
     getUserAccount().then((res) =>{
            // console.log(res)
        var that = this;
        that.formV.repairMan = res.data.userName
        that.formV.repairService = res.data.phone
        that.formV.serviceArea = localStorage.getItem("serviceArea")
      }).catch((error)=>{
        
      })
      
     $(function(){ 
      // ios兼容
     $('textarea,input').on('blur', function (event) {
        if (!(event.relatedTarget && (event.relatedTarget.tagName === 'INPUT' || event.relatedTarget.tagName === 'TEXTAREA')))
            document.body.scrollTop = 0;
    });


    })

  },

}

</script>


<style scoped>

</style>



