<template>
  <div>
    
	<div class="top"></div>
     <div class="nav-fixed">
          <div class="nav">
          	<a @click="$router.back(-1)" class="iconfont icon-fanhui"></a>
          		<div>{{ formV.woNumber}}</div>
          	 	<p class="nav-comment" v-show="isShowbx" @click='evaluate()'>评价</router-link></p> 
          </div>
     </div>
      <div class="state-text">
           <h1>报修状态</h1>
      </div>
      <div class="state-text-box display-none">
           该工单已转单,请查看相关工单!
      </div>

     <div class="state"> 	
     	<div class="process">
     	<div class="proder-dian-box">
           	<div v-for='(item,index) in state'  class="process-dian"  :class="{'censhi': statevalue > index }">
           		{{item}}
           	</div>
     </div>
     	<div class="proder-dian-box" > 
           	<div  v-for='(item,index) in index' class="process-dian" >
           		<div class="dian" :class="{'censhi01': statevalue01 > index }"></div>
           		<div class="dian-01" :class="{'censhi02': statevalue02 > index }"></div>
           	</div>
     </div>
        		
 </div> 
<div class="processs-father">
	<div  class="processs-box">
        <div class="processs">
       <span>{{ formV.currentTime }}</span>
        <span>{{ statetext }}</span>
        </div><!-- <div class="demo-sj"></div> -->
    </div>
</div>


</div>
 
     <div class="completa-list">
     	<div class="satisfied display-none"></div>
     	
     	<h1>报修信息</h1>
     	<ul>
     		<li><span>客户名称:</span><a>{{ formV.custName }}</a></li>
     		<li><span>部门:</span><a>{{ formV.department }}</a></li>
     		<li><span>工程师:</span><a>{{ formV.enginnerName }}</a></li>
     		<li><span>工程师号码:</span><a>{{ formV.enginnerPhone }}</a></li>
     		<li><span>服务类型:</span><a>{{ formV.faultType }}</a></li>
     		<li><span>服务类别:</span><a>{{ formV.accidentType }}</a></li>
     		<li><span>服务需求:</span><a>{{ formV.describe }}</a></li>
     	</ul>
     </div>

      <div class="completa-list completa-list-decice" v-show="isShowas">    
       <div class="decice-for-box">
               <span>零件订购清单</span>
              <!--   <span class="decice-green">已通过</span> -->
             </div>  
           <ul v-for='(va,index) in formV.partMessage'>
               <li><span>零件{{ index+1 }} (型号 数量) :</span><a>{{ va }}<span>件</span></a></li> 
          </ul>
     </div>


  </div>
</template>

<script>

    export default {
      name: 'RepairStatusTo',   
    
      data () {

    return {
      isShowbx: false,
      isShowas: false,
      index: 6,
      formV: '',
      state: ['待受理','已受理','已电话响应','已到达','处理中','已完成'],
      statetext:{
       b:'请耐心等待，该工单即将受理',
       c:'该工单已经受理',
       d:'工程师已致电客户',
       e:'工程师扫码到达现场,正在处理中',
       f:'工程师扫码到达现场，正在处理中',
       g:'零件订购中，请耐心等待二次上门',
       h:'该工单已经维修完成，请评价',
      },
      // statevalue:[1,2,3,4,5,6],
      //  statevalue01:[1,2,3,4,5,6],
      //   statevalue02:[1,2,3,4,5,6]
    }
  },
     methods:{
      getIndex(index){  
         var statevalue=this.$route.query.name; 
        if(statevalue=='待受理'){
          this.statetext=this.statetext.b;
          this.statevalue=1;
          this.statevalue01=0;
          this.statevalue02=1;
        }
        if(statevalue=='已受理'){
          this.statevalue=2;
          this.statevalue01=1;
          this.statevalue02=2;
          this.statetext=this.statetext.c;
        }
        if(statevalue=='已电话响应'){
          this.statevalue=3;
          this.statevalue01=2;
          this.statevalue02=3;
          this.statetext=this.statetext.d;
        }
        if(statevalue=='已到达'){
          this.statevalue=4;
          this.statevalue01=3;
          this.statevalue02=5;
          this.statetext=this.statetext.e;
        }
        if(statevalue=='处理中'){
          this.statevalue=5;
          this.statevalue01=4;
          this.statevalue02=5;
          this.statetext=this.statetext.f;
        }
        if(statevalue=='已完成'){
          this.statevalue=6;
          this.statevalue01=6;
          this.statevalue02=6;
          this.statetext=this.statetext.h;
          this.isShowbx=true;
        }
        if(statevalue=='待订件'){
          this.state[4]="待订件";
          this.statevalue=5;
          this.statevalue01=4;
          this.statevalue02=5;
          this.statetext=this.statetext.g;
          this.isShowas=true;
        }else{
           this.isShowas=false;

        }
        
},
      getData(){
          var that = this
          that.formV = JSON.parse(sessionStorage.getItem('repairIndexV'))
          // console.log(that.formV);
        },
     evaluate(){
      var that = this
          that.formV = JSON.parse(sessionStorage.getItem('repairIndexV'))
      var statevalue=that.formV.woNumber;
      this.$router.push({path:'/customer/evaluate',query:{name:statevalue}})
    }
    },
   created(){     
    this.getIndex();
    this.getData()  
    var statevalue=this.$route.query.name; 

  },



}

</script>


<style scoped>

</style>



