<template>
<!-- 审批单 -->
  <div>
    
     <div class="nav-fixed">
          <div class="nav">
             工单
          </div>
     </div>
     <div class="top"></div>


<!-- 工单 -->
			<div class="receipt">
				<router-link to='/competent/orderIndex'>已关单</router-link>
        <router-link  to='/competent/alreadyOrder'>已接单</router-link>
        <router-link  class="receipt-color" to='/competent/examineOrder'>审批单</router-link>
        <router-link to='/competent/completeOrder'>已完成</router-link>
			</div>

      <!--   已接单 -->

                <a @click="details(list)" v-for='list in items'>
                    <div class="received-box" id="stay-approval">
                         <div class="received-time">
                              <span>{{ list.repairTime }}</span>
                         </div>
                         <div class="received-box1">
                              <div class="received-box2">
                              <h1 class="iconfont icon-shebei1"><a>{{ list.woNumber }}</a></h1>
                              <span :class='list.examine'>{{ list.woState }}</span>
                              </div>
                              <h2 class="received-name">客户名称:<span>{{ list.custName }}</span></h2>
                              <h2 class="event-name-1">服务类型:<span>{{ list.faultType }}</span></h2>
                              <div class="received-box3">
                              <h1 class="received-name">故障描述:<span>{{ list.accidentType }}</span></h1>
                              <a>查看详情>></a>
                              </div>
                         </div>
                    </div>
		        		</a>

          <div v-show='showgd' style="text-align: center; font-size: 2.5rem;padding: 1.5rem 0;color: #666666">暂无工单！</div>
                   
		<!-- 底部 -->
          <div class="footer-none"></div>
	 <footer class="footer1">
    <footer class="footer">
    	<section class="on">
    		<router-link to="/competent/index">
    			<i class="iconfont icon-shouye"></i>
    			<div>首页</div>
    		</router-link>			    
    	</section>
    	<section>
    		<router-link to="/competent/orderIndex">
    			<i  style='color:#7898ff' class="iconfont icon-gongdan1"></i>
    			<div style='color:#7898ff'>工单查询</div>
    		</router-link>
    	
    	</section>
    	<section>
    		<router-link to="/competent/maintainIndex">
    			<i class="iconfont icon-_shebeibaoyang"></i>
    			<div>保养查看</div>
    		</router-link>
    	
    	</section>
    	<section>
    		<router-link to="/competent/dutyState">
    			<i class="iconfont icon-yuangongguanli"></i>
    			<div>在岗状态</div>
    		</router-link>			
    	</section>
    </footer>
  </footer>


 </div>
</template>

<script type="text/javascript">
import {TechnicalNotOverOrder} from '../../../axiosConfig/axiosUtil'	
export default {
	name: 'OrderIndex',   
    data () {
		return {
			formV: {
				woStatus : '5'
			},
			items: '',
      showgd:false,
      isActive:false,
      isA:false,
      isB:false,
      isC:false,
		}
	},
    methods:{
		getData(){
			var that = this
			TechnicalNotOverOrder(that.formV)
			.then((res) => {
         if(res.data.data==''){
              this.showgd=true;
            }
				if(res.data.code == 0){
					for(var i = 0,l = res.data.data.length;i<l;i++){
						res.data.data[i].repairTime = that.$date(res.data.data[i].repairTime)
						for(var v in res.data.data[i]){
							if(!res.data.data[i][v]){
								res.data.data[i][v] = '暂无'
							}
						} 
            if(res.data.data[i].woState == '已通过'){
               res.data.data[i].examine = 'examine1'
            }else if(res.data.data[i].woState == '已驳回'){
                res.data.data[i].examine = 'examine2'
            }else if(res.data.data[i].woState == '已完成'){
                res.data.data[i].examine = 'examine'
            }  
					}
					that.items = res.data.data					
				}

			})
		},
		details(str){
			var that = this
			// console.log(str)
			sessionStorage.setItem('examineOrderV',JSON.stringify(str))
			if(str.woState == '待审批'){
				that.$router.push({path: '/competent/pendingOrders'})
			}else if(str.woState == '已通过'){
				that.$router.push({path: '/competent/adoptOrder'})
			}else if(str.woState == '已驳回'){
				that.$router.push({path: '/competent/reject'})
			}
		}
	}, 
	created(){
		this.getData()
	},

}

</script>


<style scoped>

</style>
