<template>
	<div>
  <div class="login">
    <div class="login-img"><img src="static/img/login.png"></div>
  
      <form class="login-message">
      <ul>
        <li><span class="iconfont icon-geren"></span> <input v-model='formV.userName' placeholder="请输入用户名" type="text" name=""></li>
        <li><span class="iconfont icon-mima"></span> <input v-model='formV.password' placeholder="请输入密码" type="password" name=""></li>		
      </ul>

    <!--  <div class="radio"> 
      <div><input  type="radio" class="radio-style" checked="" name="radio">  <label>客户</label></div>
      <div><input  type="radio" class="radio-style" name="radio"> 
       个人</div>
      </div> -->
       <a class="submit" @click='loginFn()'>登录</a>
      </form>
      
     </div>
     <div style="width: 100%; height:100%;z-index: 1; position: fixed; bottom: 0; background: #009b82" > </div>
  </div>
</template>

<script>
	import {getCode,WeChatDologin,getUser} from '../axiosConfig/axiosUtil'
	import { mapGetters } from 'vuex'
export default {
  name: 'Login',
  data () {
    return {
			srcc: '',
			formV: {
				userName: '',
				password: '',
				// code: ''
			}
    }
  },
	mounted(){
		// localStorage.removeItem('openId')
		// this.$store.dispatch('user')
		
	},
	computed: {
		...mapGetters({
			id : 'user'
		})
	},
	methods:{
		getUrl(){
			var that = this
			var ge = this.$route.query.openId
			var uName = localStorage.getItem('userName')
			var Pword = localStorage.getItem('password')
			if(uName){
				that.formV.userName = uName
			}
			if(Pword){
				that.formV.password = Pword
			}
			// that.getName(ge)
		},
// 		getName(openId){
// 			var that = this
// 			if(openId){
// 				getUser(openId)
// 				.then((res) => {
// 					if(res.data.code == 0){
// 						that.formV.userName = res.data.userName
// 						that.formV.password = res.data.password
// 					}
// 				})
// 			}
// 		},
		loginFn(){
			var that = this;
			WeChatDologin(that.formV)
			.then((res) => {
				// return false
				if(res.data.code == 0){
					console.log(res)
					that.$layerMsg(res.data.msg)
					localStorage.setItem('openId',res.data.openId)
					localStorage.setItem('userName',res.data.userName)
					localStorage.setItem('password',res.data.password)
					localStorage.setItem('role',res.data.userRole)
					localStorage.setItem('serviceArea',res.data.serviceArea)
					that.$store.dispatch('user')
					that.$openId = that.id
					that.$router.push({path: '/'+res.data.userRole+'/index'})
					location.reload()
				}else{
					that.$layerMsg(res.data.msg)
					// that.$router.push({path: '/'+res.data.userRole+'/index',query:{a:1}})
				}
				
			})
		},
		fo(){
			for(var i = 0;i<1000;i++){
				this.loginFn()
			}		
		},
	},
	created(){
		this.getUrl()
		// this.fo()
		$(function(){
			$('input,textarea').on('blur',function(){
			  window.scroll(0,0);
			});
			$('select').on('change',function(){
				window.scroll(0,0);
			});
		})		
		
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login{height:100%;position: relative;z-index: 99}
</style>



