<template>
	<div>
	<div class="top-order"></div>
     <div class="nav-fixed">
          <div class="nav">
          	<a @click="$router.back(-1)"  class="iconfont icon-fanhui"></a>
          		<div>工单详情</div>
          		<a></a> 
          </div>
     </div>

     <div class="details-table">
     	<div class="details-table-title">
     		<div class="company">{{formV.custName}}</div>			
     		<div class="job-number">工单号:<span>{{formV.woNumber}}</span></div>

     	</div>
     	<div class="details-table-box">
     		<ul>
      		<li><span>服务区域</span><span>{{formV.serviceArea}}</span></li>
      		<li><span>联系人</span><span>{{formV.repairMan}}</span></li>
      		<li><span>联系号码</span><span>{{formV.repairPhone}}</span></li>
      		<li><span>服务类型</span><span>{{formV.faultType}}</span></li>
      		<li><span>服务类别</span><span>{{formV.accidentType}}</span></li>
      		<li><span>需求描述</span><span id="number">{{formV.describe}}</span></li>
      	</ul>
     	</div>
     </div>
<!-- 转单弹窗 -->

<!-- <div class="category display-none">
 			<div class="category-order">
 				<h1>转单原因</h1>
 				<div class="category-order-box">
 				<textarea class="textword" placeholder="这是帮助文字"></textarea>
 				<div class="order-btn">
 					<span class="order-btn01">取消</span>
 					<span  class="order-btn02">确认转单</span>
 				</div>

 				</div>
 			</div>
 		</div> -->

 		<!-- 受理接单 -->

		<!-- 致电客户弹窗 -->
		<div class="acceptance" :class="{'display-none':isN}">
 			<div class="acceptance-order-ot">
 				<div class="call "> 
					<h1>确认致电客户</h1>
					<div class="category-order-box" style="padding: inherit;">
						<div class="acceptance-order-btn">
							<span class="acceptance-order-btn01" @click="callCustSureNo()">取消</span>
							<a style="color: #7598ff;" class="acceptance-order-btn02" :href="'tel:' + formV.repairPhone" @click="callCustSure()">确认</a>
						</div>
					</div>
 				</div>
 			</div>
 		</div>
		 <!-- 确认致电客户弹窗 -->
		<div class="acceptance" :class="{'display-none':isY}">
 			<div class="acceptance-order-ot">
 				<div class="problem"> 
					<h1>问题已解决？</h1>
					<div class="category-order-box">
						<div class="acceptance-order-btn">
							<span  class="problem-order-btn01" @click="callCustSolveNo()">否</span>
							<span  class="problem-order-btn02" @click="callCustSolve()">是</span>
						</div>
					</div>
 				</div>
 			</div>
 		</div>

 		<div class="footer-none-two"></div>
          <div class="bottom">
               <a  @click="callCust">
				   <div class="complete-btn">致电客户</div>
			   </a>
               <div  class="solution-btn" @click="sceneSolveScan()">现场解决</div>
          </div>
          

</div>
</template>

<script>
import {phoneOrder,phoneOrderIsOver} from '../../../axiosConfig/axiosUtil'
export default {
	name: 'CloseOrders02',    
	data () {
		return {
		  formV: '',
		  isN: true,
		  isY: true,
		}
	},
    methods:{
		getData(){
			var that = this
			that.formV = JSON.parse(sessionStorage.getItem('alreadyorderV'))
			if(that.formV.woState == '已致电，请选择是否解决'){
				that.isY = false
			}
		},
		// 是否致电客户
		callCust(){
			var that = this;
			that.isN = false;
		  	that.isY = true;			
		},
		//确认致电客户
		callCustSure(){
			var that = this;
			that.isN = true;
		  	that.isY = false;
			phoneOrder(that.formV)
			.then((res) => {
				// console.log(res)
			})
		},
		//取消致电客户
		callCustSureNo(){
			var that = this;
			that.isN = true;
		  	that.isY = true;
		},
		//问题已解决
		callCustSolve(){
			var that = this;
			that.isN = true;
		  	that.isY = true;
			that.formV.isOver = 1
			// console.log(that.formV)
			phoneOrderIsOver(that.formV)
			.then((res) => {
				if(res.data.code == 0){
					that.$layerMsg(res.data.msg)
					if(that.formV.faultType == '事故类'){		
						// alert(1)
						that.$router.push({path:'/engineer/sceneSolveFeedback'})
					}else{				
						that.$router.push({path:'/engineer/demandFeedback'})
					}					
				}else{
					that.$layerMsg(res.data.msg)
				}
			})
			
		},
		//问题未解决
		callCustSolveNo(){
			var that = this;
			that.isN = true;
		  	that.isY = true;
			that.formV.isOver = 0
			phoneOrderIsOver(that.formV)
			.then((res) => {
				if(res.data.code == 0){
					that.$layerMsg(res.data.msg)
					if(that.formV.faultType == '事故类'){						
						that.$router.push({path:'/engineer/sceneSolveScan'})
					}else{							
						that.$router.push({path:'/engineer/demandScan'})
					}
				}else{
					that.$layerMsg(res.data.msg)
				}
			})
			
		},
		sceneSolveScan(){
			var that = this;
			let woNumber = that.formV.woNumber;
			if(that.formV.faultType == '事故类'){					
				that.$router.push({path:'/engineer/sceneSolveScan'})
			}else{				
				that.$router.push({path:'/engineer/demandScan'})
			}
			// this.$router.push({path:'/engineer/sceneSolveScan'})			
			// sessionStorage.setItem('workNum',woNumber)
		
		}
		
	}, 
   created(){    
	this.getData()
  },
}

</script>


<style scoped>

</style>



