<template>
  <div>
  
    <div class="nav-fixed">
          <div class="nav">
            <a @click=$router.back(-1) class="iconfont icon-fanhui"></a>
              <div>待关单工单列表</div>
              <a></a> 
          </div>
     </div>
     <div class="top"></div>




        <!--   待关单 -->
          <a @click='details(list)' v-for='list in items'>
               <div class="demand-box">
               <div class="demand-time">
                    <span>{{ list.repairTime }}</span>
               </div>
               <div class="demand-box1">
                    <div class="demand-box2">
                    <h1 class="iconfont icon-shebei1"><a>{{ list.woNumber }}</a></h1>
                    </div>
                    <h2 class="demand-name">客户名称:<span>ABB{{ list.custName }}</span></h2>
                     <h2 class="demand-name-1">服务类型:<span>{{ list.faultType }}</span></h2>
                    <div class="demand-box3">
                    <h1 class="demand-name">需求描述:<span>{{ list.describe }}</span></h1>
                    <a>查看详情>></a>
                    </div>
               </div>
               </div>
          </a>
          <div v-show='showgd' style="text-align: center; font-size: 2.5rem;padding: 1.5rem 0;color: #666666">暂无工单！</div>

</div>
</template>

<script>
import {TechnicalNotOverOrder} from '../../../axiosConfig/axiosUtil'	
export default {
  name: 'CloseOrderList',   
  data () {
    return {
			formV: {
				woStatus: '1' //待关单
			},
			items: '',
      showgd: '',
    }
  },
  methods:{  
		getData(){
			var that = this			
			that.formV.woStatus = 1
			TechnicalNotOverOrder(that.formV)
			.then((res) => {
        if(res.data.data==''){
              this.showgd=true;
            }
				if(res.data.code == 0){
					// res.data.data.repairTime = that.$date(res.data.data.repairTime)
					for(var i = 0,l = res.data.data.length;i<l;i++){
						res.data.data[i].repairTime = that.$date(res.data.data[i].repairTime)
						for(var v in res.data.data[i]){
							if(!res.data.data[i][v]){
								res.data.data[i][v] = '暂无'
							}
						}
					}
					that.items = res.data.data					
				}
			})
		},
		details(str){
			var that = this
			sessionStorage.setItem('closeOrderListV',JSON.stringify(str))
			that.$router.push({path:'/competent/closeOrder'})
		}
	},
  created(){
		this.getData()
  },
}

</script>


<style scoped>

</style>
