<template>
  <div>
    
 
	<div class="top"></div>
     <div class="nav-fixed">
          <div class="nav">
          	<router-link to='/customer/index' class="iconfont icon-fanhui"></router-link>
          		<div>扫一扫报修</div>
          		<a></a> 
          </div>
     </div>

     <div class="code">
     	<div>机器编码</div>
     	 <input type="" v-model='formV.machCode'  placeholder="请输入机器编码" name="">
     </div>

      <div class="categoryimg" v-show="isshowimg">
      <div id="picture" style="height: 100%; display: flex;align-items: center;justify-content: center;"></div>
      </div>


      <div class="details-box">
            <ul>
              <li><span>设备名称</span><span>{{ formV.devName }}</span></li>
              <li><span>设备型号</span><span>{{ formV.unitType }}</span></li>
              <li><span>设备位置</span><span>{{ formV.location }}</span></li>
              <li><span>客户名称</span><span>{{ formV.custArea }}</span></li>
              <li><span>部门</span><span>{{ formV.department }}</span></li>
            </ul>
      </div>

       <div class="details-box" style="margin-top: 2.5rem">
            <ul>
              <li><span><em class="red">*</em> 联系人</span> <input  v-model.trim='formVC.repairMan' maxlength="50" type="" name="" placeholder="请输入联系人"></li>
              <li><span><em class="red">*</em> 联系号码</span> <input v-model.trim='formVC.repairService' maxlength="11" type="" name="" placeholder="请输入联系号码"></li>
              <li><span><em class="red">*</em> 服务类别</span><span class="select" @click='isShowtk'><span class="select-val">{{ formVC.accidentType }}</span><a>▼</a></span></li>
              <li><span><em class="red">*</em> 故障描述</span><span class="select" @click='isShowtv'><span class="select-val">{{ formVC.remark }}</span><a>▼</a></span></li>
              <li v-show='isShowD'><span><em class="red">*</em> 其它故障</span> <input v-model.trim='formVC.other' type="" name="" placeholder="其它故障描述" style="width: 70%"></li>
							<li><span class="iconfont icon-tianjiatupian1"><em class="red " style="visibility: hidden;">*</em> 添加故障图片 <input class="input"  type="file" id="file" accept="image/*" mutiple="mutiple" /></span> 
              <span></span>
              </li> 
              
            </ul>
             <span id="img" style="margin-top: 2rem"> 
               <a class="notext"></a></span> 
        </div>


 <!-- 	报修申请成功 -->
 		<div class="acceptance" v-show='isshowb'>
 			<div class="acceptance-order-ot">
 				<div class="call"> 
 				<h1>报修申请成功</h1>
 				<div class="category-order-box">
 				<div class="acceptance-order-btn">
 					<span class="problem-btn01" v-on:click="index">确定</span>
 				</div>
				</div>
 				</div>
 			</div>
 		</div>
 		 <!-- 	报修申请成功 -->
     <div class="footer-none"></div>
 		<div class="bottom">
 			<div class="repair-btn" @click='subMit()'>提交报修</div>
 		</div>



 		<div class="category" v-show='isshowtk'>
      <div class="category-4">
      <ul v-for='vas in category'>
        <label ><li  @click='ac(vas)'>{{vas}}<input type="radio" name="category"></li></label>
      </ul>
      </div>
    </div>
		
		<div class="category" v-show='isshowtv'>
			<div class="category-4"  style="height:28.3rem;">
			<ul v-for='vasA in gzms'>
				<label ><li  @click='ad(vasA)'>{{vasA}}<input type="radio" name="category"></li></label>
			</ul>
			</div>
		</div>	

 </div>
</template>

<script type="text/javascript">
import {customerScannerOrder,getUserAccount,maintenancePlanByMachCode} from '../../../axiosConfig/axiosUtil'
export default {
  name: 'AccidentScan',
  data () {
    return {
       formV: {
          machCode: '' ,
        },
				formVC: {
					machCode: '' ,
				  repairMan: '' ,
				  repairService: '' ,
				  faultType: '事故类' ,
				  accidentType: '',
				  remark: '' ,
				   picture01:'',
				  picture02:'',
				  picture03:'',
				},
         category: ['软件故障','硬件故障','耗材供断'],
				 gzms: ['图像异常','卡纸','错误代码','机械故障','其它'],
         isshowtk: false,
         isshowb: false,
         isshowimg:false,
				 isshowtv: false,
				 isShowD: false				 
     
    }
  },
  mounted(){
    
  },
  methods:{ 
     index: function () {
      this.$router.push({path:'/customer/index'})
    },
    getData(){
      var that = this;
      var statevalue=this.$route.query.sbb;
			// alert(statevalue)
      if(typeof(statevalue) == 'undefined'){
        var url = location.href;
            var machCode = getUrl(url,"machCode");
            function getUrl(url,name) {
                var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
                var r = url.split("?")[1].match(reg);
                if (r != null)
                    return decodeURI(r[2]);
                return null;
            }  
           this.formV.machCode = machCode;
					 that.formVC.machCode=this.formV.machCode
      }else{
          this.formV.machCode=statevalue;
					that.formVC.machCode=this.formV.machCode
      }
        
      // console.log(that.formV)
      maintenancePlanByMachCode(that.formV)
      .then((res) => { 
        that.formV=res.data.data;
        // console.log(res)       
      })
      getUserAccount().then((res) =>{
            // console.log(res)
        that.formVC.repairMan = res.data.userName
        that.formVC.repairService = res.data.phone
      }).catch((error)=>{
        
      })
  },
   subMit(){
    var that = this 
      // console.log(that.formV)
// 			if(!(/^1[34578]\d{9}$/.test(that.formVC.repairService))){ 
// 			     that.$layerMsg("手机号码有误，请重填");  
// 			}else{
					customerScannerOrder(that.formVC)
					.then((res) => {       
						// console.log(res)
						 if(res.data.code == 0){
							 that.$layerMsg("报修成功")
							 that.$router.push({path:'/customer/index'})             
						}else{ 
							that.$layerMsg(res.data.msg)
						}
					})
			// }
      
  },
   ac(str){ 
      var that = this
      that.formVC.accidentType = str
      that.isshowtk = false
    },
		ad(str){ 
		     var that = this
		     that.formVC.remark = str
					if(str == "其它"){
						that.isShowD = true
					}else{
						that.isShowD = false
						that.formVC.other = ''
					}
		     that.isshowtv = false
		   },
     isShowtk: function () {
       this.isshowtk = true
    },
		isShowtv: function () {
		   this.isshowtv = true
		},

  },
  created(){
    var that = this;
    this.getData()  
    var statevalue=this.$route.query.sbb;
		this.indexUrl = localStorage.getItem('openId');
     $(function(){ 
       $('textarea,input').on('blur', function (event) {
        if (!(event.relatedTarget && (event.relatedTarget.tagName === 'INPUT' || event.relatedTarget.tagName === 'TEXTAREA')))
            document.body.scrollTop = 0;
    });
        $(document).on("click",".spancolor",function(){
          $(this).parent().remove();
        })
        $(document).on("click","#one",function(){ 
         var img =$(this).attr("src");
         $("#picture").append("<img style='width:100%;' src="+img+">");
          that.isshowimg=true;
        })
        $(document).on("click","#picture",function(){
          $("#picture").find("img").remove();
          that.isshowimg=false;
        })
      $("#file").change(function(){
         var m_this = this;
         cutImageBase64(m_this,null,400,0.7);  
          })
       function cutImageBase64(m_this,id,wid,quality) {
          var a=0;
          var file = m_this.files[0];
          var URL = window.URL || window.webkitURL;
          var blob = URL.createObjectURL(file);
          var base64;
          var img = new Image();
          img.src = blob;
          img.onload = function() {
              var thats = this;
              //生成比例
              var w = thats.width,
                  h = thats.height,
                  scale = w / h;
                  w = wid || w;
                  h = w / scale;
      //生成canvas
              var canvas = document.createElement('canvas');
              var ctx = canvas.getContext('2d');
              $(canvas).attr({
                  width: w,
                  height: h
              });
            ctx.drawImage(thats, 0, 0, w, h);
              // 生成base64            
           base64 = canvas.toDataURL('image/jpeg', quality || 0.7);
           var imgleng = $('#img .divcolor').length;
     if(imgleng == 0){
      that.formVC.picture01=base64;  
         $("#img").append("<div class='divcolor'><span class='spancolor'>×</span><img id=one src="+this.src+"></div>")
         $(".notext").remove();
     }
      if(imgleng == 1){ 
      that.formVC.picture02=base64;  
         $("#img").append("<div class='divcolor'><span class='spancolor'>×</span><img id=one src="+this.src+"></div>")

         $(".notext").remove();
     }
      if(imgleng == 2){
      that.formVC.picture03=base64;  
         $("#img").append("<div class='divcolor'><span class='spancolor'>×</span><img id=one src="+this.src+"></div>")
         // $("#one").attr("src",this.src);
         // $("#one").css("width",60)
         $(".notext").remove();
          // console.log(base64)
     }
     if(imgleng == 3){
      that.$layerMsg("最多上传三张图片") 
     }
      
        
    };
}



      })
  }
}
</script>


<style scoped>

</style>



