<template>
  <div>
  
	<div class="top"></div>

          <div class="confirm iconfont icon-tishi- display-none"> 请先扫码确认到达现场</div>

     <div class="nav-fixed">
          <div class="nav">
          	<a @click="$router.back(-1)" class="iconfont icon-fanhui"></a>
          		<div>审批</div>
          		<a></a> 
          </div>
     </div>
    <!--  详情列表 -->
     <div class="site-details">
     	<ul>
               <li><span>机器编码:</span><a>{{ formV.machCode }}</a></li> 
               <li><span>设备名称:</span><a>{{ formV.devName }}</a></li> 
               <li><span>设备型号:</span><a>{{ formV.unitType }}</a></li>
               <li><span>设备位置:</span><a>{{ formV.location }}</a></li>
               <li><span>客户名称:</span><a>{{ formV.custName }}</a></li>
               <li><span>部门:</span><a>{{ formV.department }}</a></li>
               <li><span>联系人:</span><a>{{ formV.repairMan }}</a></li>
               <li><span>联系号码:</span><a>{{ formV.repairPhone }}</a></li>
               <li><span>服务类型:</span><a>{{ formV.faultType }}</a></li>
               <li><span>服务类别:</span><a>{{ formV.accidentType }}</a></li>
			   <li><span>黑白读数:</span><a>{{ formV.bwReader }}</a></li>
			   <li><span>彩色读数:</span><a>{{ formV.coReader }}</a></li>
               <li><span>故障描述:</span><a>{{ formV.describe }}</a></li>
               <li><span>工程师:</span><a>{{ formV.enginnerName }}</a></li>
               <li><span>工程师电话:</span><a>{{ formV.enginnerPhone }}</a></li>
          </ul>
     </div>

        <div class="completa-list">      
          <h1>零件订购清单</h1>
          <ul v-for="(vaa,index) in formV.partMessage">
               <li><span>零件{{ index+1 }} (型号 数量) :</span><a>{{ vaa }} 件</a></li>
               <!-- <li><span>零件2 (型号 数量) :</span><a>BBB 5件</a></li> -->
          </ul>
		  <ul>
		  	<li><span>订购原因:</span><a>{{ formV.orderReason }}</a></li> 
		  </ul>
     </div>


               <!-- 驳回原因< -->
          <div class="category" v-show="isShow">
               <div class="category-order">
                    <h1>驳回原因</h1>
                    <div class="category-order-box">
                    <textarea class="textword" v-model='form.content' placeholder="请输入驳回原因"></textarea>
                    <div class="order-btn">
                         <span v-on:click="toggle()" class="order-btn01">取消</span>
                         <span class="order-btn02"> <a @click='TechnicalRejectedOrder()' style="color: #fff">确认驳回</a></span>
                    </div>

                    </div>
               </div>
          </div>

          <div class="footer-none-two"></div>
          <div class="bottom">
               <div @click='TechnicalPartOrder()' class="adopt-btn">通过</div>
               <div @click="toggle()" class="rejected-btn">驳回</div>
          </div>


</div>
</template>

<script>
import {TechnicalPartOrder,TechnicalRejectedOrder} from '../../../axiosConfig/axiosUtil'	
export default {
	name: 'PendingOrders',       
	data () {
		return {
		  isShow: false,
		  formV: '',
             va: ['待受理','已受理','已电话响应','已到达','处理中','已完成'],
		  form: {
			  woNumber: '',
			  content: ''
		  },
		}
	},
    methods:{
		toggle:function(){
			this.isShow = !this.isShow;
		},
		getData(){
		  var that = this
		  that.formV = JSON.parse(sessionStorage.getItem('examineOrderV'))
		//   console.log(that.formV)
		},
		TechnicalPartOrder(){
			var that = this
			that.form.woNumber = that.formV.woNumber
			TechnicalPartOrder(that.form)
			.then((res) => {
				if(res.data.code == 0){
					that.$layerMsg(res.data.msg)
					that.$router.push({path: '/competent/examineOrder'})
				}else{
					that.$layerMsg(res.data.msg)
				}
			})
		},
		TechnicalRejectedOrder(){
			var that = this
			that.form.woNumber = that.formV.woNumber
			TechnicalRejectedOrder(that.form)
			.then((res) => {
				if(res.data.code == 0){
					that.$layerMsg(res.data.msg)
					that.$router.push({path: '/competent/examineOrder'})
				}else{
					that.$layerMsg(res.data.msg)
				}
			})
		}

	}, 
	created(){      
		this.getData()
          $(function(){
               $('input,textarea').on('blur',function(){
                     window.scroll(0,0);
                     });
               $('select').on('change',function(){
                     window.scroll(0,0);
                     });
              })
	},


}

</script>


<style scoped>

</style>



