<template>
     <div>
	<div class="top"></div>
     <div class="nav-fixed">
          <div class="nav">
          	<a  @click="$router.back(-1)" class="iconfont icon-fanhui"></a>
          		<div>{{formV.woNumber}}</div>
              <a></a>
          </div>
     </div>
 
     <div class="completa-list">
     	<div :class="formV.woState"></div>
     	
     	<h1>报修信息</h1>
     	<ul>
     		<li><span>服务区域:</span><a>{{ formV.serviceArea }}</a></li>
     		<li><span>联系人:</span><a>{{ formV.repairMan }}</a></li>
     		<li><span>联系号码:</span><a>{{ formV.repairPhone }}X</a></li>
     		<li><span>服务类型:</span><a>{{ formV.faultType }}</a></li>
     		<li><span>服务类别:</span><a>{{ formV.accidentType }}</a></li>
     		<li><span>需求描述:</span><a>{{ formV.describe }}</a></li>
     	</ul>
     </div>

      <div class="completa-list display-none ">      
           <div class="decice-for-box">
               <span>零件订购清单</span>
             </div>
          <ul >
               <li><span>零件1 (型号 数量) :</span><a>ABB 5件</a></li>
               <li><span>零件2 (型号 数量) :</span><a>BBB 5件</a></li> 
          </ul>
     </div>

</div>
</template>

<script>
    export default {
      name: 'RepairStatusto',   
    
        data () {
    return {
      formV: ''
    }
  },
    methods:{
    getData(){
      var that = this
      that.formV = JSON.parse(sessionStorage.getItem('completeOrderV'))
      // console.log(that.formV)
    }
  }, 
   created(){    
  this.getData()
  },
}

</script>

<style scoped>

</style>





