<template>
  <div>
        <div class="nav-fixed">
          <div class="nav">
            <a></a>
              <div>在岗状态</div>
              <span @click='toggle()' class="iconfont icon-shaixuan" id="screen"></span> 
          </div> 

<!--           隐藏域 -->
    <div class="qipao-box" v-if='isShow'>
        <div class="qipao"  @click='edit(list2,index)' v-for='(list2,index) in item'>
          <div v-bind:class='{bg:index==isactive}'>{{ list2 }}</div>         
        </div>
        <div class="demo"></div>
    </div>
    <!--           隐藏域 -->

     </div> 
     <div class="top"></div>
     <!-- 在岗状态 -->
     <div class="sou">
      <i class="iconfont icon-sousuo"></i>
      <input type="" name="" id="dutyinput" @keyup='edit2(formV.staffName)' v-model='formV.staffName' placeholder="请输入姓名">
    </div>

    <div class="personnel">
      <ul v-for='(list,index) in items'>
        <li>
          <div class="personnel-text"><div>{{ list.name }}</div> <span>{{ list.phone }}</span></div>
          <p :style='"color:"+list.colorA'>{{ list.workCond }}</p>
        </li>      
      </ul>
    </div>
     <div v-show='showgd' style="text-align: center; font-size: 2.5rem;padding: 1.5rem 0;color: #666666">暂无数据！</div>

<!-- 底部 -->
  <div class="footer-none"></div>
  <footer class="footer1">
    <footer class="footer">
    	<section class="on">
    		<router-link to="/competent/index">
    			<i class="iconfont icon-shouye"></i>
    			<div>首页</div>
    		</router-link>			    
    	</section>
    	<section>
    		<router-link to="/competent/orderIndex">
    			<i class="iconfont icon-gongdan1"></i>
    			<div>工单查询</div>
    		</router-link>
    	
    	</section>
    	<section>
    		<router-link to="/competent/maintainIndex">
    			<i class="iconfont icon-_shebeibaoyang"></i>
    			<div>保养查看</div>
    		</router-link>
    	
    	</section>
    	<section>
    		<router-link to="/competent/dutyState">
    			<i  style='color:#7898ff' class="iconfont icon-yuangongguanli"></i>
    			<div style='color:#7898ff'>在岗状态</div>
    		</router-link>			
    	</section>
    </footer>
  </footer>

</div>
</template>

<script>
import {TechnicalWorkingStatus} from '../../../axiosConfig/axiosUtil'	
export default {
	name: 'DutyState',
	data () {
		return {
			formV: {
				staffName: '',
				state: ''
			},
			items: '',
			isShow: false,
      showgd:false,
      isactive:4,
			item: ['休假','培训','在岗'],
      color: {
        red:false,
        green:false,
        yellow:false,
       
      }
     
		}
  },
  methods:{
		toggle(){
			this.isShow = !this.isShow
		},
		edit(str,index){
      this.isactive=index;
			var that = this
			this.formV.state = str
			that.isShow = false
			TechnicalWorkingStatus(this.formV)
			.then((res) => {
           if(res.data.data==''){
              that.showgd=true;
            }else{
               that.showgd=false;
            }
        for(var i=0;i<res.data.data.length;i++){
          if(res.data.data[i].workCond == "在岗"){
             res.data.data[i].colorA = 'green'
          }else if(res.data.data[i].workCond=="培训"){
             res.data.data[i].colorA = 'yellow'
          }else if(res.data.data[i].workCond=="休假"){
             res.data.data[i].colorA = 'red'
          }
        }
				if(res.data.code == 0){
					that.items = res.data.data
				}else{
					that.$layerMsg(res.data.msg)
				}
			})
		},
		edit2(str){
			var that = this
			this.formV.staffName = str
			that.isShow = false
			TechnicalWorkingStatus(this.formV)
			.then((res) => {
        if(res.data.data==''){
              that.showgd=true;
            }else{
               that.showgd=false;
            }

      for(var i=0;i<res.data.data.length;i++){
        if(res.data.data[i].workCond == "在岗"){
           res.data.data[i].colorA = 'green'
        }else if(res.data.data[i].workCond=="培训"){
           res.data.data[i].colorA = 'yellow'
        }else if(res.data.data[i].workCond=="休假"){
           res.data.data[i].colorA = 'red'
        }
      }
				if(res.data.code == 0){
					that.items = res.data.data
				}else{
					that.$layerMsg(res.data.msg)
				}
			})
		},
		getData(){
			var that = this
			TechnicalWorkingStatus(that.formV)
			.then((res) => {        
				if(res.data.code == 0){
          // console.log(res.data.data);
          for(var i=0;i<res.data.data.length;i++){
            if(res.data.data[i].workCond == "在岗"){
               res.data.data[i].colorA = 'green'
            }else if(res.data.data[i].workCond=="培训"){
               res.data.data[i].colorA = 'yellow'
            }else if(res.data.data[i].workCond=="休假"){
               res.data.data[i].colorA = 'red'
            }
          }
					that.items = res.data.data
				}else{
					that.$layerMsg(res.data.msg)
				}
			})
		},
  }, 
  created(){
		this.getData()
  },
}

</script>


<style scoped>

</style>



