<template>
  <div>
	  <div class="top"></div>
     <div class="nav-fixed">
          <div class="nav">
          	<a class="iconfont icon-fanhui" @click="$router.back(-1)"></a>
          		<div>事故报修类</div>
          		<a></a> 
