 					<span v-on:click="index" class="problem-btn01">确定</span>
 				</div>
				</div>
 				</div>
 			</div>
 		</div>
 		 <!-- 	报修申请成功 -->

 		
 		<div class="category" v-show='isshowtk'>
 			<div class="category-4">
