 				<label ><li  @click='ac(vas)'>{{vas}}<input type="radio" name="category"></li></label>
 			</ul>
 			</div>
 		</div>
 		 
		<div class="category" v-show='isshowtv'>
			<div class="category-4" style="height:28.3rem;">
			<ul v-for='vasA in gzms'>
				<label ><li  @click='ad(vasA)'>{{vasA}}<input type="radio" name="category"></li></label>
			</ul>
			</div>
		</div>	

</div>
</template>
<script>
</script>
<script type="text/javascript">
 import {customerScannerOrder,getUserAccount,maintenancePlanByMachCode} from '../../../axiosConfig/axiosUtil'
// import {maintenancePlanByMachCode} from '../../../axiosConfig/axiosUtil'
  export default {
      name: 'Accident',   
  data () {
    return {
        formV: {
          machCode: '' ,
          repairMan: '' ,
          repairService: '' ,
          faultType: '' ,
          accidentType: '',
          remark: '' ,
          unitType: '' ,
          devName: '' ,
          location: '' ,
          custArea: '' ,
          department: '' ,
           openId:'',
        },
        formVC: {
         machCode: '' ,
          repairMan: '' ,
          repairService: '' ,
          faultType: '' ,
          accidentType: '',
          remark: '' ,
          unitType: '' ,
          devName: '' ,
          location: '' ,
          custArea: '' ,
          department: '' ,
           openId:'',
          picture01:'',
          picture02:'',
          picture03:'',
					other: ''
        },
        isShow: false,
        isShowa:true,
        isshowb: false,
        isshowtk: false,
				isshowtv: false,
				isShowD: false,
        isshowimg:false,
        category: ['软件故障','硬件故障','耗材供断'],
