    sure(){
    var that = this 
      // console.log(that.formV)
      maintenancePlanByMachCode(that.formV)
      .then((res) => { 
        // console.log(res)
         if(res.data.code == 1){
             this.isShow=false;
             this.isShowa=true;
             that.$layerMsg(res.data.msg)
        }else{    
