<template>
  <div>
	<div class="top"></div>

    <div class="confirm iconfont icon-tishi- display-none"> 请先扫码确认到达现场</div>

     <div class="nav-fixed">
          <div class="nav">
          	<a  @click="$router.back(-1)" class="iconfont icon-fanhui"></a>
          		<div>现场解决</div>
          		<a></a> 
          </div>
     </div>
    <!--  详情列表 -->
     <div class="site-details">
     	   <ul>
               <li><span>工单号:</span><a>{{ formV.woNumber }}</a></li> 
               <li><span>机器编码:</span><a>{{ formV.machCode }}</a></li> 
               <li><span>客户名称:</span><a>{{ formV.custName }}</a></li> 
			   <li><span>客户地址:</span><a>{{ formV.location }}</a></li> 
               <li><span>联系人:</span><a>{{ formV.repairMan }}</a></li>
               <li><span>联系号码:</span><a>{{ formV.repairPhone }}</a></li>
               <li><span>服务类型:</span><a>{{ formV.faultType }}</a></li>
               <li><span>需求描述:</span><a>{{ formV.describe }}</a></li> 
               <li><span>提出时间:</span><a>{{ formV.repairTime }}</a></li>
          </ul>
     </div>

<!-- 零件订购 -->
  <div class="popup" v-if='isShowA'>
       <div class="popup-1">
         <h1>零件订购</h1>
            <div class="popup-2" v-for='(vb,index) in items'>
                <input type="text" v-model='vb.model' name="" placeholder="零件耗材名称">&nbsp—&nbsp
                <input type="text" v-model='vb.number' name="" placeholder="数量">
                <span class="iconfont icon-shanchu" @click='removeNew(index)'></span>
            </div>
			
	        <div class="popup-3" @click='addNew()'>
				<span class="iconfont icon-tianjia"></span>
			</div>  
			<div class="popup-2">
			    <input type="text" name="" style='width:90%' v-model='formV.orderReason' placeholder="订购原因">
			</div>
			
			
	       <div class="scene-btn">
	            <span class="cancel" @click='isShowA=false'>取消</span>
	            <span class="sub" @click='subMit()'>确认提交</span>
	       </div>
       </div>
  </div>


          <!-- 转单原因 -->
          <div class="category" :class="{'display-none':isShow}">
               <div class="category-order">
                    <h1>转单原因</h1>
                    <div class="category-order-box">
                    <textarea class="textword" v-model='turnOrderReson' placeholder="请输入转单原因"></textarea>
                    <div class="order-btn">
                         <span class="order-btn01" @click='isShow=true'>取消</span>
                         <span  class="order-btn02" @click='isTurnOrder()'>确认转单</span>
                    </div>

                    </div>
               </div>
          </div>

			<div class="bottom">
				<a  v-on:click="sceneSolveFeedback"  class="sweep-btn">维修完成反馈</a>
			   <div class="upgrade-btn" @click='isShowA=true'>零件订购</div>
			   <div class="scenebtn-order-btn" @click='turnOrder()'>转单升级</div>
			</div>
	

</div>
</template>

<script>
	
import {arriveOrderAccident} from '../../../axiosConfig/axiosUtil'
import {turnOrder,partsOrder} from '../../../axiosConfig/axiosUtil'	
export default {
    name: 'Reject',       
    data () {
		return {
		  newTxt: '',
		  formV: {
		  	woNumber: '',
		  	machCode: '',
			orderReason: ''
		  },
		  machCode: '',
		  turnOrderReson: '',
		  isShow: true,
		  isShowA: false,
		  toId: '2',
		  items: [
			  {id:1,model: '',number: ''},			
		  ],
		  
		}
	},
     methods:{
    sceneSolveFeedback: function () {
      this.$router.push({path:'/engineer/sceneSolveFeedback'})
    },
	// 确认转单
	isTurnOrder(){
		var that = this
		that.formV.turnOrderReson = that.turnOrderReson
		turnOrder(that.formV)
		.then((res) => {
			if(res.data.code == 0){
				that.$layerMsg(res.data.msg)
				that.$router.push({path: '/engineer/catchSheet'})
			}else{
				that.$layerMsg(res.data.msg)
			}
		})
	},
	subMit(){
		var that = this
		that.formV.partsTypeNumber = that.items
		partsOrder(that.formV)
		.then((res) => {
			if(res.data.code == 0){
				that.$layerMsg(res.data.msg)
				that.$router.push({path: '/engineer/examineOrder'})
			}else{
				that.$layerMsg(res.data.msg)
			}
		})
	},
	addNew(){
		var that = this		
		if(that.toId > 5){
			that.$layerMsg('零件订购不得大于5条!')
			return false;
		}else{
			that.items.push({
				id: that.toId++,
				model: '',
				number: ''
			})
		}
	},
	removeNew(str){
		var that = this
		if(that.items.length > 1){			
			that.items.splice(str,1)
		}else{
			that.$layerMsg('零件订购必须大于0条!')
			return false
		}
		
		
	},
	// 转单升级
	turnOrder(){
		var that = this
		that.isShow = false	
	},
	getData(){
		var that = this;
		that.formV = JSON.parse(sessionStorage.getItem('alreadyorderV'))
		console.log(that.formV)
	  	var statevalue=this.$route.query.sbb;
	  	this.formV.woNumber=that.formV.woNumber
		this.formV.machCode=statevalue;
		console.log(this.formV.machCode)
		console.log(statevalue)
		if(that.formV.woState != '已到达现场'){
			arriveOrderAccident(that.formV)
			.then((res) => { 
				console.log(res);
				console.log(this.machCode)
				if(res.data.code == 1){
					that.$layerMsg(res.data.msg);
				}else{ 
					this.isshowb=true;
				}

			})	
		}else{
			
		}
		
		that.formV = JSON.parse(sessionStorage.getItem('alreadyorderV'))
		console.log(that.formV.woNumber)
	}

  }, 
   created(){
    var statevalue=this.$route.query.sbb;
	this.getData()
	}
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



