<template>
  <div>
    
	<div class="top-order"></div>
     <div class="nav-fixed">
          <div class="nav">
          	<a @click="$router.back(-1)" class="iconfont icon-fanhui"></a>
          		<div>工单详情</div>
          		<a></a> 
          </div>
     </div>

     <div class="details-table">
     	<div class="details-table-title">
     		<div class="company">{{ formV.custName }}</div>
     		<div class="job-number">工单号:<span>{{ formV.woNumber }}</span></div>
     	</div>
     	<div class="details-table-box">
     		<ul>
      		<li><span>机器编码</span><span>{{ formV.machCode }}</span></li>
      		<li><span>设备名称</span><span>{{ formV.devName }}</span></li>
      		<li><span>设备型号</span><span>{{ formV.unitType }}</span></li>
      		<li><span>部门</span><span>{{ formV.department }}</span></li>
      		<li><span>联系人</span><span>{{ formV.repairMan }}</span></li>
      		<li><span>联系号码</span><span>{{ formV.repairPhone }}</span></li>
      		<li><span>设备位置</span><span>{{ formV.location }}</span></li>
      		<li><span>服务类型</span><span>{{ formV.faultType }}</span></li>
      		<li><span>服务类别</span><span>{{ formV.accidentType }}</span></li>
      		<li><span>故障代码</span><span>{{ formV.faultNo }}</span></li>
      		<li><span>故障描述</span><span>{{ formV.describe }}</span></li>
      		<li><span>提出时间</span><span>{{ formV.repairTime }}</span></li>
      		<li><span>工程师</span><span>{{ formV.enginnerName }}</span></li>
      		<li><span>工程师电话</span><span id="number">{{ formV.enginnerPhone }}</span></li>
			<li><span>耗材型号及数量</span><span>{{ formV.serviceType }}</span></li>	
      		<li><span>故障照片</span><span class="faultImg">
					 	{{showNull}}
					 	<img v-for="(item,index) in picture" :key="index" :src="item.custName" @click="pictureBig(index)">
					</span></li>
      		<div class="order-img">
      		</div>
      	</ul>
     	</div>
     </div>
<!-- 转单弹窗 -->

    <div class="category" :class="{'display-none':isN}">
 			<div class="category-order">
 				<h1>转单原因</h1>
 				<div class="category-order-box">
 				<textarea class="textword" v-model='formVa.turnOrderReson' placeholder="请输入转单原因"></textarea>
 				<div class="order-btn">
 					<span class="order-btn01" @click='isN=true'>取消</span>
 					<span  class="order-btn02" @click='trueChangeOrder()'>确认转单</span>
 				</div>

 				</div>
 			</div>
 		</div>

 		<!-- 受理接单 -->

    <div class="acceptance" v-show="isShow">
 			<div class="acceptance-order-ot">
 				<div class="problem"> 
     			<h1>是否接受工单？</h1>
              <div class="acceptance-order-btn">
                <span v-on:click="toggle()" class="problem-order-btn01">取消</span>
                <span @click='acceptOrder()' class="problem-order-btn02"><a style=" color: #7598ff; display: inline-block;">确定</a></span>
              </div>
 				</div>

 			</div>
 		</div>

 			<div class="footer-none"></div>
         <!-- <div class="bottom">
               <div  v-on:click="toggle()" class="complete-btn">接受工单</div>
          </div> -->
		<footer class="order-footer1" id="ds">
			<footer class="order-footer">
				<section id="order" @click='changeOrder()' class="change-order">转单</section>
				<section @click='toggle()' class="acceptance-order">受理工单</section>
			</footer>
		</footer>
	<div class="picShade" v-if="isShade" @click="picShade()">
		<img :src="pictureB">
	</div>

</div>
</template>

<script>
import {acceptOrder2,startTurnOrder,woNumberPicture} from '../../../axiosConfig/axiosUtil'	 
export default {
	name: 'CloseOrder', 
	data () {
		return {
			isShow:false,
			formV: '',
			formVa: {
				turnOrderReson: '',
				woNumber: ''
			},
			isN: true,
			picture: '',
			isShade: false,
			pictureB: '',
			showNull: '',
		}
	},
  methods:{   
	  	//转单
		changeOrder(){
			this.isN = false
		},
		trueChangeOrder(){
			var that = this		
			startTurnOrder(that.formVa)
			.then((res) => {
				if(res.data.code == 0){
					that.$layerMsg(res.data.msg)
					that.$router.push({path: '/competent/index'})
				}else{
					that.$layerMsg(res.data.msg)
				}
			})
		},
		toggle(){
				this.isShow = !this.isShow;
		},
		getData(){
		  var that = this
		  that.formV = JSON.parse(sessionStorage.getItem('stayOrderV'))
		  that.formVa.woNumber = that.formV.woNumber
		},
		acceptOrder(){
			var that = this
			acceptOrder2(that.formV)
			.then((res) => {
				if(res.data.code == 0){
					that.$layerMsg(res.data.msg)
					that.$router.push({path: '/competent/alreadyOrder'})
				}else{
					that.$layerMsg(res.data.msg)
				}
			})
		},
		pictureBig(inx){
			var that = this
			// console.log(inx)
			// console.log(that.picture)
			
			that.isShade = true
			that.pictureB = that.picture[inx].custName
			// console.log(that.pictureB,"1111")
		},
		picShade(){
			this.isShade = false 
		}
	}, 
  created(){
      this.getData()
	  let picFault = {woNumber:this.formV.woNumber}
		woNumberPicture(picFault).then((res) => {
				if(res.data.msg == '对不起，该工单暂无图片'){
					this.showNull = '暂无图片'
				}else{
					this.picture = res.data.data
				}
			})
	}
}

</script>


<style scoped>

</style>



