<template>
<div id="app">
  <div class="nav-fixed">
          <div class="nav">
             首页
          </div>
     </div>
     <div class="top"></div>
  <!-- 首页 -->
  <div class="home">
  <!-- 轮播图 -->
    <div class="img">
    </div>
<!-- 报修 -->
    <div class="box">     
      <div class="repair_1">
        <div class="repair">        
          <section>
              <a @click="sys_click()">
             <i class="iconfont icon-weihubaoyang icon-shiguleixingtongji_1"></i>
          <div>扫一扫保养</div>
              </a>
          </section>

          <section>
             <a @click="sys_click02()">
          <i class="iconfont icon-saoyisao1 icon-shiguleixingtongji_2"></i>
               <div>扫一扫报修</div>
            </a>
          </section>      
        </div>
      </div>
    </div>

    <div class="competent-scheme">
      <div class="scheme-title">
          <div>待接工单</div>
          <router-link to="/engineer/wholeOrder">查看更多>></router-link>
      </div>

          <a v-for="list in items" @click='details(list)'>
            <div class="competent-box receipt-mg" id="event">
               <div class="competent-box1">
                    <div class="competent-box2">
                    <h1 class="iconfont icon-shebei1"><a>{{ list.woNumber }}</a></h1>  
                    <span>{{ list.faultType }}</span>
                    </div>
                    <h2 class="competent-name">客户名称:<span>{{ list.custName }}</span></h2>
                     <h2 class="competent-name-1">服务类别:<span>{{ list.accidentType }}</span></h2>
										 <h2 class="competent-name-1">{{ list.dd }}:<span>{{ list.describe }}</span></h2>
                 <div class="competent-box3">
                    <h1 class="competent-name">报修时间:<span>{{ list.repairTime }}</span></h1>
                     <a>查看详情>></a>
                 </div>
               </div>
            </div>
          </a>
  <div v-show='showgd' style="text-align: center; font-size: 2.5rem;padding: 1.5rem 0;color: #666666;background: #fff" >暂无工单！</div>
      </div>

    <!--  常见故障解决方案 -->
    <div class="scheme">
      <router-link to='/engineer/som' class="scheme-title">
          <div>SOM操作说明</div>
          <a>查看更多>></a>
      </router-link>
    </div>
    </div>

    <!-- 底部 -->
<div class="footer-none"></div>
		  <footer class="footer1">
		    <footer class="footer">
		      <section  class="on">
				<router-link class='this' :to='{path: "/engineer/index"}'>
					<i  style='color:#7898ff' class="iconfont icon-shouye"></i>
					<div style='color:#7898ff'>首页</div>
				</router-link>      
		      </section>
			<section>
				<router-link class='this' :to='{path: "/engineer/alreadyorder"}'>
					<i class="iconfont icon-gongdan"></i>
					<div>工单</div>				
				</router-link>
			</section>      
			<section>
				<router-link class='this' :to='{path: "/engineer/index03"}'>
					<i class="iconfont icon-_shebeibaoyang"></i>
					<div>保养查看</div>
				</router-link>      
			</section>
		    </footer>
		  </footer>

         <!-- 展示路由 -->
  <!-- <router-view></router-view> -->
</div>
</template>

<script>
import wx from 'weixin-js-sdk'
import {enginnerOrder} from '../../../axiosConfig/axiosUtil'
export default {
	name: 'Index',       
	data () {
    return {
			formV: {
				// openId: sessionStorage.getItem('openId'),
				woStatus: '1',
			},
			items: '',
      showgd:false,
      dd: ''
    }
  },
	mounted(){
		sessionStorage.removeItem('engineerIndexV')
	},
  methods:{  
      sys_click(){ 
        var that = this;
        var url=location.href;
        $.get("http://47.106.110.201:80/som/getSign?url="+url, function(data){    
            wx.config({
                // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                debug: false,
                // 必填，公众号的唯一标识
                appId: data.appId,
                // 必填，生成签名的时间戳
                timestamp: "" + data.timestamp,
                // 必填，生成签名的随机串
                nonceStr: data.noncestr,
                // 必填，签名
                signature: data.signature,
                // 必填，需要使用的JS接口列表，所有JS接口列表
                jsApiList: ['checkJsApi', 'scanQRCode']
            });

        });
        wx.error(function (data) {
            alert("出错了：" + data.errMsg);//这个地方的好处就是wx.config配置错误，会弹出窗口哪里错误，然后根据微信文档查询即可。
        });
 
        wx.ready(function () {
            wx.checkJsApi({
                jsApiList: ['scanQRCode'],
                success: function (data) {
                }
            });
			wx.scanQRCode({
				needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
				scanType: ["qrCode"], // 可以指定扫二维码还是一维码，默认二者都有
				success: function (data) {
					var result = data.resultStr; // 当needResult 为 1 时，扫码返回的结果
					var ac = result.split('&')
					var icc = 8
					var acc = JSON.stringify(ac[1])
					// alert("扫描结果："+result);
					var vals = acc.substring(68,acc.length-1)
					if(vals){
					  // alert(vals)
					  that.$router.push({path: '/engineer/scanMaintain',query:{sbb: vals}}),
					  location.reload()
					}else{
					  // alert(1)
					}
					// window.location.href = result;//因为我这边是扫描后有个链接，然后跳转到该页面
				}
			}); 
        }); 
        
        },

         sys_click02(){ 
        var that = this ;
         var url=location.href;
        $.get("http://47.106.110.201:80/som/getSign?url="+url, function(data){    
            wx.config({
                // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                debug: false,
                // 必填，公众号的唯一标识
                appId: data.appId,
                // 必填，生成签名的时间戳
                timestamp: "" + data.timestamp,
                // 必填，生成签名的随机串
                nonceStr: data.noncestr,
                // 必填，签名
                signature: data.signature,
                // 必填，需要使用的JS接口列表，所有JS接口列表
                jsApiList: ['checkJsApi', 'scanQRCode']
            });

        });
        wx.error(function (data) {
            alert("出错了：" + data.errMsg);//这个地方的好处就是wx.config配置错误，会弹出窗口哪里错误，然后根据微信文档查询即可。
        });
 
        wx.ready(function () {
            wx.checkJsApi({
                jsApiList: ['scanQRCode'],
                success: function (data) {
                }
            });
                wx.scanQRCode({
                    needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
                    scanType: ["qrCode"], // 可以指定扫二维码还是一维码，默认二者都有
                    success: function (data) {
                        var result = data.resultStr; // 当needResult 为 1 时，扫码返回的结果
                        var ac = result.split('&')
                        var icc = 8
                        var acc = JSON.stringify(ac[1])                        
                        // alert("扫描结果："+result);
                        var vals = acc.substring(68,acc.length-1)
                        if(vals){
                          // alert(vals)
                          that.$router.push({path: '/engineer/scan',query:{sbb: vals}}),
						  location.reload()
						  
                        }else{
                          // alert(1)
                        }
                        // window.location.href = result;//因为我这边是扫描后有个链接，然后跳转到该页面
                    }
                });
 
        }); 
        },
		getData(){
			var that = this
			enginnerOrder(that.formV)
			.then((res) => {
				if(res.data.code == 0){
          if(res.data.data==''){
              this.showgd=true;
            }else{
              this.showgd=false;
            }
					// res.data.data.repairTime = that.$date(res.data.data.repairTime)
					for(var i = 0,l = res.data.data.length;i<l;i++){
						res.data.data[i].repairTime = that.$date(res.data.data[i].repairTime)
            if(res.data.data[i].faultType == '事故类'){
              res.data.data[i].dd = '故障描述'
              // that.dd == '故障描述'
            }else{
              // alert(2)
              res.data.data[i].dd = '需求描述'
              // that.dd == '需求描述'
            }
						for(var v in res.data.data[i]){
							if(!res.data.data[i][v]){
								res.data.data[i][v] = '暂无'
							}
						}
					}
					that.items = res.data.data
				}
			})
		},
		details(str){
			var that = this
			sessionStorage.setItem('engineerIndexV',JSON.stringify(str))
			// console.log(str)
			if(str.faultType == '事故类'){
				that.$router.push({path: '/engineer/workDetails'})
			}else{
				that.$router.push({path: '/engineer/demandClass02'})
			}
			
		}
  }, 
  created(){   
		this.getData()
  },
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



