<template>
  <div>
	<div class="top"></div>

          <!-- <div class="confirm iconfont icon-tishi- display-none"> 请先扫码确认到达现场</div> -->

     <div class="nav-fixed">
          <div class="nav">
          	<a @click="$router.back(-1)" class="iconfont icon-fanhui"></a>
          		<div>现场解决</div>
          		<a></a> 
          </div>
     </div>
    <!--  详情列表 -->
     <div class="site-details">
           	<ul>
               <li><span>工单号:</span><a>{{ formV.woNumber }}</a></li> 
               <li><span>机器编码:</span><a>{{ formV.machCode }}</a></li>
               <li><span>客户名称:</span><a>{{ formV.custName }}</a></li> 
               <li><span>客户地址:</span><a>{{ formV.location }}</a></li> 
               <li><span>联系人:</span><a>{{ formV.repairMan }}</a></li>
               <li><span>联系号码:</span><a>{{ formV.repairPhone }}</a></li>
               <li><span>服务类型:</span><a>{{ formV.faultType }}</a></li>
               <li><span>需求描述:</span><a>{{ formV.describe }}</a></li> 
               <li><span>提出时间:</span><a>{{ formV.repairTime }}</a></li>
          </ul>
     </div>

          <div class="bottom" >
          <!--   <router-link to='/engineer/sceneSolve02'></router-link> -->
             <!--    <div v-on:click="suba" class="upgrade-btn " id="sweep-code"> 手动到达</div> -->
               <div v-on:click="sys_click()" class="upgrade-btn iconfont icon-saoyisao" id="sweep-code"> 到达扫码</div>
          </div>
       
</div>
</template>

<script>
  import wx from 'weixin-js-sdk'
	import {arriveOrderOther,arriveOrderAccident} from '../../../axiosConfig/axiosUtil'
    export default {
      name: 'closeOrders02',   
    
      data () {
        return {
          formV: {
            machCode: '',
					// machCode: ''
        },
        isshow:false,
      }
    },
      mounted() {
       
      },
     methods:{  
      sys_click(){ 
        var that = this;
        var url=location.href;
        that.formV = JSON.parse(sessionStorage.getItem('alreadyorderV'))
        $.get("http://47.106.110.201:80/som/getSign?url="+url, function(data){    
            wx.config({
                // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                debug: false,
                // 必填，公众号的唯一标识
                appId: data.appId,
                // 必填，生成签名的时间戳
                timestamp: "" + data.timestamp,
                // 必填，生成签名的随机串
                nonceStr: data.noncestr,
                // 必填，签名
                signature: data.signature,
                // 必填，需要使用的JS接口列表，所有JS接口列表
                jsApiList: ['checkJsApi', 'scanQRCode']
            });

        });
        wx.error(function (data) {
            alert("出错了：" + data.errMsg);//这个地方的好处就是wx.config配置错误，会弹出窗口哪里错误，然后根据微信文档查询即可。
        });
 
        wx.ready(function () {
            wx.checkJsApi({
                jsApiList: ['scanQRCode'],
                success: function (data) {
                }
            });
                wx.scanQRCode({
                    needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
                    scanType: ["qrCode"], // 可以指定扫二维码还是一维码，默认二者都有
                    success: function (data) {
                        var result = data.resultStr; // 当needResult 为 1 时，扫码返回的结果
                        var ac = result.split('&')
                        var icc = 8
                        var acc = JSON.stringify(ac[1])                        
                        // alert("扫描结果："+result);
                        var vals = acc.substring(68,acc.length-1)
                       if(vals==that.formV.machCode){
                          // alert(vals)
                          that.$router.push({path: '/competent/sceneSolve02',query:{sbb: vals}})
                        }else{
                             that.$layerMsg('对不起，该机器编码与工单里的机器编码不匹配!')
                        }
                        // window.location.href = result;//因为我这边是扫描后有个链接，然后跳转到该页面
                    }
                });
 
        }); 
        



        },
			getData(){
				var that = this
				that.formV = JSON.parse(sessionStorage.getItem('alreadyorderV'))
        // console.log(that.formV.machCode)
			}



    }, 
   created(){
    this.getData();
  },


}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
