import * as types from './mutation-types'
// 类似于事件 每个mutation都有字符类型的事件类型和回调函数
// 全局引入vue
import Vue from 'vue'
import Axios from 'axios'
Vue.prototype.$http = Axios

export default {
  [types.user] (state) {
    state.user =  sessionStorage.getItem('openId') || ''
    
    // state.testlanguage = sessionStorage.getItem('testlanguage')
    // console.log(state.userInfo.headimg)
  }
}



