<template>
<div>
     <div class="nav-fixed">
          <div class="nav">
             保养计划
          </div>
     </div>
     <div class="top"></div>

     <!-- 保养计划 -->
           <div class="maintain-frame">
               <div class="maintain" @click='details(list)'  v-for='list in formV' id="maintain">
                    <div class="maintain-one">
                    <h1>合同编号:<span>{{ list.contractNo }}</span></h1>
                     <h1>客户名称:<span>{{ list.custName }}</span></h1>
                      <!-- <h1>保养频率:<span>月</span></h1> -->
                    </div>
                     <a class="iconfont icon-chakanxiangqing"></a>
               </div>
          </div>
     <!-- 保养计划 -->
     <div v-show='showgd' style="text-align: center; font-size: 2.5rem;padding: 1.5rem 0;color: #666666">暂无保养计划！</div>

		<!-- 底部 -->
<div class="footer-none"></div>
		  <footer class="footer1">
		    <footer class="footer">
		      <section  class="on">
				<router-link class='this' :to='{path: "/engineer/index"}'>
					<i class="iconfont icon-shouye"></i>
					<div>首页</div>
				</router-link>      
		      </section>
			<section>
				<router-link class='this' :to='{path: "/engineer/alreadyorder"}'>
					<i class="iconfont icon-gongdan"></i>
					<div>工单</div>				
				</router-link>
			</section>      
			<section>
				<router-link class='this' :to='{path: "/engineer/index03"}'>
					<i  style='color:#7898ff' class="iconfont icon-_shebeibaoyang"></i>
					<div style='color:#7898ff'>保养查看</div>
				</router-link>      
			</section>
		    </footer>
		  </footer>

</div>
</template>

<script>
import {EngMaintenanceContract} from '../../../axiosConfig/axiosUtil'
export default {
	name: 'index',
	data () {
		return {
			formV: '',
			showgd:false,
    }
  },
  methods:{
		getData(){
			var that = this
			EngMaintenanceContract()
			.then((res) => {
				if(res.data.data==''){
	            this.showgd=true;
	          }
				if(res.data.code == 0){
					that.formV = res.data.data
				}
			})
		},
		details(str){
			var that = this
			sessionStorage.setItem('index03V',JSON.stringify(str))
			that.$router.push({path: '/engineer/deciceNo'})
		}
	}, 
  created(){
		this.getData()
  },


}

</script>


<style scoped>

</style>



