import Router from 'vue-router'
import Login from '@/components/login.vue'
import Login2 from '@/components/login2.vue'
import Engineer from '@/components/engineer/engineer.vue'
import Register from '@/components/engineer/register.vue'
import Customer from '@/components/customer/customer.vue'
import Index from '@/components/engineer/index/index.vue'
import Index03 from '@/components/engineer/index/index03.vue'
import WorkDetails from '@/components/engineer/work-order-details/workDetails.vue'
import DemandClass02 from '@/components/engineer/work-order-details/demandClass02.vue'
import Alreadyorder from '@/components/engineer/order/alreadyorder.vue'
import ExamineOrder from '@/components/engineer/order/examineOrder.vue'
import CompleteOrder from '@/components/engineer/order/completeOrder.vue'
import CatchSheet from '@/components/engineer/order/catchSheet.vue'
import CloseOrder02 from '@/components/engineer/received-order/closeOrder02.vue'
import SceneSolveScan from '@/components/engineer/received-order/sceneSolveScan.vue'
import SceneSolveFeedback from '@/components/engineer/received-order/sceneSolveFeedback.vue'
import SceneSolve02 from '@/components/engineer/received-order/sceneSolve02.vue'
import Demand from '@/components/engineer/received-order/demand.vue'
import DemandScan from '@/components/engineer/received-order/demandScan.vue'
import DemandFeedback from '@/components/engineer/received-order/demandFeedback.vue'
import PendingOrders from '@/components/engineer/approval/pendingOrders.vue'
import Reject from '@/components/engineer/approval/reject.vue'
import AdoptOrder from '@/components/engineer/approval/adoptOrder.vue'
import RepairStatus from '@/components/engineer/order/repairStatus.vue'
import TransferredOrder from '@/components/engineer/received-order/transferredOrder.vue'
import DemandOrder from '@/components/engineer/received-order/demandOrder.vue'
import Decice from '@/components/engineer/device-information/decice.vue'
import DeciceNo from '@/components/engineer/device-information/deciceNo.vue'
import WholeOrder from '@/components/engineer/whole-order/wholeOrder.vue'
import RepairStatusto from '@/components/engineer/order/repairStatusto.vue'
import ScanMaintain from '@/components/engineer/scan/scanMaintain.vue'
import Scan from '@/components/engineer/scan/scan.vue'
import Som from '@/components/engineer/device-information/som.vue'


export default [
	{
	  path: '/',
	  name: 'Login',
	  component: Login
	},
	{
	  path: '/engineer',
	  name: 'Engineer2',
	  component: Engineer,
	  children: [
		{
		  path: 'index',
		  name: 'Index2',
		  component: Index,
		
		},
		{
		  path: 'som',
		  name: 'Som',
		  component: Som,
		
		},
		{
		  path: 'login2',
		  name: 'Login2',
		  component: Login2
		},
		{
		  path: 'scanMaintain',
		  name: 'ScanMaintain',
		  component: ScanMaintain,
		
		},
		{
		  path: 'register',
		  name: 'Register',
		  component: Register,
		
		},
		{
		  path: 'scan',
		  name: 'Scan',
		  component: Scan,
		
		},
		{
			path: 'wholeOrder',
			name: 'WholeOrder2',
			component: WholeOrder
		},
		{
		  path: 'index03',
		  name: 'Index032',
		  component: Index03,
		
		},
		{
		  path: 'repairStatusto',
		  name: 'RepairStatusto',
		  component: RepairStatusto,
		
		},
		{
		  path: 'alreadyorder',
		  name: 'Alreadyorder2',
		  component: Alreadyorder,
		
		},
		{
		  path: 'examineOrder',
		  name: 'ExamineOrder2',
		  component: ExamineOrder,
		
		},
		 {
		  path: 'completeOrder',
		  name: 'CompleteOrder2',
		  component: CompleteOrder,
		
		},
		{
		  path: 'catchSheet',
		  name: 'CatchSheet2',
		  component: CatchSheet,
		
		},
		{
		  path: 'closeOrder02',
		  name: 'CloseOrder022',
		  component: CloseOrder02,
		
		},
		 {
		  path: 'sceneSolveScan',
		  name: 'SceneSolveScan2',
		  component: SceneSolveScan,
		
		},
		{
		  path: 'sceneSolveFeedback',
		  name: 'SceneSolveFeedback2',
		  component: SceneSolveFeedback,
		
		},
		{
		  path: 'sceneSolve02',
		  name: 'SceneSolve022',
		  component: SceneSolve02,
		
		},
		{
		  path: 'demand',
		  name: 'Demand2',
		  component: Demand,
		
		},
		,
		{
		  path: 'demandScan',
		  name: 'DemandScan2',
		  component: DemandScan,
		
		},
		{
		  path: 'demandFeedback',
		  name: 'DemandFeedback2',
		  component: DemandFeedback,
		
		},
		 {
		  path: 'transferredOrder',
		  name: 'TransferredOrder2',
		  component: TransferredOrder,
		
		},
		{
		  path: 'demandOrder',
		  name: 'DemandOrder2',
		  component: DemandOrder,
		
		},
		 {
		  path: 'pendingOrders',
		  name: 'PendingOrders2',
		  component: PendingOrders,
		
		},
		{
		  path: 'reject',
		  name: 'Reject2',
		  component: Reject,
		
		},
		{
		  path: 'adoptOrder',
		  name: 'AdoptOrder2',
		  component: AdoptOrder,
		
		},
		 {
		  path: 'repairStatus',
		  name: 'RepairStatus2',
		  component: RepairStatus,
		
		},
		 {
		  path: 'decice',
		  name: 'Decice2',
		  component: Decice,
		
		},	
		 {
		  path: 'deciceNo',
		  name: 'DeciceNo2',
		  component: DeciceNo,
		
		},				
		 {
		  path: 'workDetails',
		  name: 'WorkDetails2',
		  component: WorkDetails
		},
		{
		  path: 'demandClass02',
		  name: 'DemandClass022',
		  component: DemandClass02
	    },
	]
  },

]




