import Vue from 'vue'
import App from './App'
import router from './router'
import '../static/css/index.css'
import axios from 'axios'
import $ from 'jquery'
import store from './vuex/store'
// import $ from 'jquery'
// import "babel-polyfill"
import promise from 'es6-promise'
promise.polyfill()
import Calendar from 'vue2-datepick';
Vue.use(Calendar);
// console.log(axios)
var host = 'http://47.106.110.201:80/som/'    //线上
// var host = 'http://192.168.1.135:80/som/'    //本地
Vue.prototype.$exportE = url => {
	window.open(host+url)
}

Vue.prototype.$axios = axios

Vue.prototype.$openId = sessionStorage.getItem('openId')
Vue.prototype.$layerMsg = str => {
    layui.use('layer',function(){layer.msg(str)})
}
Vue.prototype.$layerClose = str => {
    layui.use(['form','table','layer'], function(){
    	var table = layui.table, layer = layui.layer;
    	layer.closeAll()
    	table.reload(str)						
    })
}
Vue.prototype.$exportEa = url => {
	$.ajax({
		url: host+url,
		type:'get',
		success : function(res){
			window.open('http://'+res)
		},
		error : function(error){
			alert(error)
		}
	})
}
Vue.config.productionTip = false
// Vue.prototype.$axios = axios
Vue.prototype.$url = host
// Vue.prototype.$url = 'http://192.168.1.135:80/som/'     //本地

Vue.prototype.$date = function(date){	
	var str = parseInt(date);
	if (str != "" || str != null) {
		var oDate = new Date(str);
		var oYear = oDate.getFullYear();
		var oMonth = oDate.getMonth() + 1;
		oMonth = oMonth >= 10 ? oMonth : '0' + oMonth;
		var oDay = oDate.getDate();
		oDay = oDay >= 10 ? oDay : '0' + oDay;
		var theDate = oYear + "-" + oMonth + "-" + oDay;
	} else {
		theDate = "";
	}
	return theDate;
}
Vue.prototype.$extend = function(jsonbject1, jsonbject2) {
	  var resultJsonObject = {}
	  for (var attr in jsonbject1) {
	    resultJsonObject[attr] = jsonbject1[attr]
	  }
	  for (var attr2 in jsonbject2) {
	    resultJsonObject[attr2] = jsonbject2[attr2]
	  }
	  return resultJsonObject
	};

// Vue.prototype.$that = this
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



