import Vue from 'vue'
import Vuex from 'vuex'
import * as getters from './getters'
import mutations from './mutations'
import * as actions from './actions'

Vue.use(Vuex)

// 全局引用cookie方法类
// const xucasuserInfo = {
//   xucasuserInfo: JSON.parse(sessionStorage.getItem('xucasuserInfo')) || '',
// }
const user = sessionStorage.getItem('openId') || ''

// const testlanguage = sessionStorage.getItem('testlanguage') || ""
const state = {
  user
}
// console.log('1111+++',userInfo.headimg)
export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions
})



