<template>
  <div>
	<div class="top-order"></div>
     <div class="nav-fixed">
          <div class="nav">
          	<a  @click="$router.back(-1)" class="iconfont icon-fanhui"></a>
          		<div>工单详情</div>
          		<a></a> 
          </div>
     </div>

     <div class="details-table">
     	<div class="details-table-title">
     		<div class="company">{{formV.custName}}</div>
     		<div class="job-number">工单号:<span>{{formV.woNumber}}</span></div>

     	</div>
     	<div class="details-table-box">
     		<ul>
      		<li><span>服务区域</span><span>{{formV.serviceArea}}</span></li>
      		<li><span>联系人</span><span>{{formV.repairMan}}</span></li>
      		<li><span>联系号码</span><span>{{formV.repairPhone}}</span></li>
      		<li><span>服务类型</span><span>{{formV.faultType}}</span></li>
      		<li><span>服务类别</span><span>{{formV.accidentType}}</span></li>
      		<li><span>需求描述</span><span>{{formV.describe}}</span></li>
      	</ul>
     	</div>
     </div>
<!-- 转单弹窗 -->
<div class="completa-list">    
       <div class="decice-for-box">
               <span>转单原因</span>
        </div>  
        <div class="turn-text">{{formV.turnOrderReson}}</div>
     </div>

</div>
</template>

<script type="text/javascript">
  export default {
      name: 'catchSheet',   
    
      data () {
    return {
       formV: ''
    }
  },
     methods:{
    index: function () {
      this.$router.push({path:'/engineer/index'})
    },
     alreadyorder: function () {
      this.$router.push({path:'/engineer/alreadyorder'})
    },
     index03: function () {
      this.$router.push({path:'/engineer/index03'})
    },
     examineOrder: function () {
      this.$router.push({path:'/engineer/examineOrder'})
    },
     completeOrder: function () {
      this.$router.push({path:'/engineer/completeOrder'})
    },
     catchSheet: function () {
      this.$router.push({path:'/engineer/catchSheet'})
    },
    getData(){
      var that = this
      that.formV = JSON.parse(sessionStorage.getItem('catchSheetV'))
      console.log(that.formV)
    }


  }, 
   created(){
          this.getData()
      $(function(){ 


      })

  },


}

</script>

<style scoped>

</style>



