        <!-- 工程师som -->
      <div class="layui-collapse somList" lay-filter="test" >
        <div class="layui-colla-item">
          <h2 class="layui-colla-title">1、关注并登陆公众号</h2>
          <div class="layui-colla-content">
            <p></p>
            <img src="static/img/som1.png">
          </div>
        </div>
        <div class="layui-colla-item">
          <h2 class="layui-colla-title">2、扫码报修</h2>
          <div class="layui-colla-content">
            <p>工程师可以在主页通过点击“扫一扫报修”来进行代客户报修；</p>
            <img src="static/img/som2.png">
          </div>
        </div>
        <div class="layui-colla-item">
          <h2 class="layui-colla-title">3、扫码保养</h2>
          <div class="layui-colla-content">
            <p>工程师可以通过点击“扫一扫保养”来启动对该设备的保养作业；（请参阅上一条图示）</p>
            
          </div>
        </div>
        <div class="layui-colla-item">
          <h2 class="layui-colla-title">4、工单受理</h2>
          <div class="layui-colla-content">
            <p>客服派单后，会利用短信通知该责任工程师，提醒有一个工单派给了该工程师，以便工程师及时登录公众号查看工单。</p>
            <img src="static/img/som3.png">
            <p>之后工程师可以进入公众号，在首页可以查看到最近一个待受理工单通过点击“待接工单”进入查看客服派给自己的工单列表；</br>点击查看详情后，可以查看待接工单的详细信息。</p>
            <img src="static/img/som4.png">
            <img src="static/img/som5.png">
            <img src="static/img/som6.png">
