        <div class="layui-colla-item">
          <h2 class="layui-colla-title">5、工单处理</h2>
          <div class="layui-colla-content">
            <p>如果该责任工程师因休假、培训或其他特殊原因无法接单，则可以点击“转单”，填写转单原因，将工单转回给客服重新派单。如果可以接单，则点击“受理工单”进入下一步，选择“致电客户”或“现场解决”来开始处理工单。</p>
            <img src="static/img/som7.png">
            <img src="static/img/som8.png">
            <img src="static/img/som9.png">
