            <img src="static/img/som10.png">
            <img src="static/img/som11.png">
            <img src="static/img/som12.png">
            <p>如果电话解决了客户的问题，而不需要去客户现场解决，则点击“电话解决”来关闭该服务工单。</br>当电话无法解决问题，或者工程师根据故障描述及服务需求描述判断无法电话解决而需要去客户现场解决时，点击“现场解决”，到达客户现场后，找到该报修设备，点击“到达扫码”以反馈到达时间。</p>
            <img src="static/img/som13.png">
            <img src="static/img/som14.png">
            <img src="static/img/som15.png">
            <p>如无需订购零件或升级处理，则开始现场维修，维修完成后，点击“维修完成反馈”并填写相关服务信息，最终点击“服务完成”以反馈该服务已完成。</br>如需订购零件，则需点击“零件及耗材订购”来输入需要订购的零件及耗材，以反馈订购需求，为下一次维修及服务做准备。</p>
            <img src="static/img/som16.png">
            <img src="static/img/som17.png">
            <img src="static/img/som18.png">
            <p>其他非事故类工单，也应填写服务需求的处理完成信息，并最终点击“服务完成”来完成工单，并反馈问题解决时间。</p>
            <img src="static/img/som19.png">
            <img src="static/img/som20.png">
          </div>
        </div>
        <div class="layui-colla-item">
          <h2 class="layui-colla-title">6、服务升级</h2>
