            <img src="static/img/som21.png">
            <img src="static/img/som22.png">

          </div>
        </div>
        <div class="layui-colla-item">
          <h2 class="layui-colla-title">7、服务节点反馈</h2>
          <div class="layui-colla-content">
            <p>SOM系统通过手机端，可以准确、及时的反馈各服务节点。</br>主要的服务节点有：</br>&bull;客户报修时间</br>&bull;客服派单时间</br>&bull;工程师接单时间</br>&bull;工程师电话响应时间</br>&bull;工程师到达现场时间</br>&bull;问题解决时间</br>&bull;转单及升级时间</p>

          </div>
        </div>
        <div class="layui-colla-item">
          <h2 class="layui-colla-title">8、保养计划及保养执行</h2>
          <div class="layui-colla-content">
            <p>工程师可以在首页的“保养查看”里，查看自己的保养任务，并在到达客户现场后，通过首页的“扫一扫保养”来扫码启动保养作业，该客户的保养计划完成情况，可以点击进入保养计划下的该客户，以获取当前保养完成情况及信息。</p>
            <img src="static/img/som23.png">
            <img src="static/img/som24.png">
            <img src="static/img/som25.png">
            <img src="static/img/som26.png">
          </div>
        </div>
        <div class="layui-colla-item">
          <h2 class="layui-colla-title">9、个人信息</h2>
          <div class="layui-colla-content">
            <p>工程师可以在手机端查看、更新个人信息，例如当手机号码更换时。</p>
          </div>
        </div>
