import Login from '@/components/login.vue'
import Customer from '@/components/customer/customer.vue'
import Index from '@/components/customer/customer-index/index.vue'
import RepairIndex from '@/components/customer/customer-index/repairIndex.vue'
import Feedbackindex from '@/components/customer/customer-index/feedbackindex.vue'
import FaultPlan from '@/components/customer/fault-plan/faultPlan.vue'
import Som from '@/components/customer/fault-plan/som.vue'
import Accident from '@/components/customer/accident/accident.vue'
import Service from '@/components/customer/accident/service.vue'	
import RepairStatus from '@/components/customer/repair-status/RepairStatus.vue'	
import RepairStatusTo from '@/components/customer/repair-status/repairStatusTo.vue'	
import Repairindexno from '@/components/customer/customer-index/repairindexno.vue'
import RepairIndexComplete from '@/components/customer/customer-index/repairIndexComplete.vue'
import Evaluate from '@/components/customer/evaluate/evaluate.vue'
import AccidentScan from '@/components/customer/accident/accidentScan.vue'



export default [
    {
      path: '/',
      name: 'Login3',
      component: Login
    },
    {
      path: '/customer',
      name: 'Cstomer',
      component: Customer,
		children: [
			{
			  path: 'index',
			  name: 'Index',
			  component: Index
			},
			{
			  path: 'repairIndex',
			  name: 'RepairIndex',
			  component: RepairIndex
			},
			{
			  path: 'repairIndex',
			  name: 'RepairIndex',
			  component: RepairIndex
			},
			{
			  path: 'som',
			  name: 'Som',
			  component: Som
			},
			{
			  path: 'feedbackindex',
			  name: 'Feedbackindex',
			  component: Feedbackindex
			},
			{
			  path: 'faultPlan',
			  name: 'FaultPlan',
			  component: FaultPlan
			},
			{
			  path: 'accident',
			  name: 'Accident',
			  component: Accident
			},
			{
			  path: 'service',
			  name: 'Service',
			  component: Service
			},
			{
			  path: 'repairStatus',
			  name: 'RepairStatus',
			  component: RepairStatus
			},
			{
			  path: 'repairindexno',
			  name: 'Repairindexno',
			  component: Repairindexno
			},
			{
			  path: 'repairStatusTo',
			  name: 'RepairStatusTo',
			  component: RepairStatusTo
			},
			// {
			//   path: 'repairIndex',
			//   name: 'RepairIndex',
			//   component: RepairIndex
			// },
			{
			  path: 'repairIndexComplete',
			  name: 'RepairIndexComplete',
			  component: RepairIndexComplete
			},
			{
			  path: 'evaluate',
			  name: 'Evaluate',
			  component: Evaluate
			},
			{
			  path: 'accidentScan',
			  name: 'AccidentScan',
			  component: AccidentScan
			},
		]
	}
]
		 



