<template>
  <div>
	<div class="top"></div>

          <!-- <div class="confirm iconfont icon-tishi- display-none"> 请先扫码确认到达现场</div> -->

     <div class="nav-fixed">
          <div class="nav">
          	<a @click="$router.back(-1)" class="iconfont icon-fanhui"></a>
          		<div>现场解决</div>
          		<a></a> 
          </div>
     </div>
    <!--  详情列表 -->
     <div class="site-details">
     	<ul>
               <li><span>工单号:</span><a>{{ formV.woNumber }}</a></li> 
               <li><span>客户名称:</span><a>{{ formV.custName }}</a></li> 
               <li><span>联系人:</span><a>{{ formV.repairMan }}</a></li>
               <li><span>联系号码:</span><a>{{ formV.repairPhone }}</a></li>
               <li><span>服务类型:</span><a>{{ formV.faultType }}</a></li>
               <li><span>需求描述:</span><a>{{ formV.describe }}</a></li> 
               <li><span>提出时间:</span><a>{{ formV.repairTime }}</a></li>
          </ul>
     </div>

      <div class="popup display-none">
               <div class="popup-1">
                    <h1>零件订购</h1>
                    <div class="popup-2">
                         <input type="" name="" placeholder="请输入型号">&nbsp—&nbsp
                         <input type="" name="" placeholder="请输入数量">
                         <span class="iconfont icon-shanchu"></span>
                    </div>
                     <div class="popup-2">
                         <input type="" name="" placeholder="请输入型号">&nbsp—&nbsp
                         <input type="" name="" placeholder="请输入数量">
                         <span class="iconfont icon-tianjia"></span>
                    </div>
               <div class="scene-btn">
                    <span class="cancel">取消</span>
                       <span class="sub">确认提交</span>
               </div>
               </div>
          </div>


          <div class="scenebtn">
               <div class="upgrade-btn"  v-on:click="demandFeedback()" >到达现场</div>
          </div>
</div>
</template>

<script>
	import {arriveOrderOther,arriveOrderAccident} from '../../../axiosConfig/axiosUtil'
    export default {
      name: 'index',   
    
      data () {
    return {

    }
  },
     methods:{
    demandFeedback() {
			var that = this
			arriveOrderOther(that.formV)
			.then((res) => {
					that.$router.push({path:'/engineer/demandFeedback'})
				
			})
      
    },
		getData(){
			var that = this
			that.formV = JSON.parse(sessionStorage.getItem('alreadyorderV'))
		}
  }, 
   created(){
     this.getData()
  },


}

</script>

<style scoped>

</style>




