<template>
  <div class="register" style="background: #fff">
        <div class="nav-fixed">
          <div class="nav">
                    <a @click="$router.back(-1)" class="iconfont icon-fanhui"></a>
                    <div>用户注册</div>
                    <a></a>
          </div>
        </div>
          <div class="top"></div>
     <div class="register">
          <ul>
               <li><span><em class="red">*</em> 手机号:</span><input v-model='formV.phone' placeholder="请输入手机号"  pattern="^1[3456789]\d{9}$" type="" name=""></li>
               <li><span><em class="red">*</em> 密码:</span><input v-model='formV.password' maxlength="16" placeholder="请输入密码" type="password" name=""></li>
               <li><span><em class="red">*</em> 确认密码:</span><input v-model='formV.newPassword' maxlength="16" placeholder="请再次输入密码" type="password" name=""></li>
               <li><span><em class="red">*</em> 姓名:</span><input v-model='formV.name' placeholder="请输入姓名" type="" name=""></li>
               <!-- <li><span><em class="red">*</em> 所属公司:</span> <textarea id="smsContent" maxlength="50" style="width: 100%; line-height: 3rem; height: 3rem" v-model='formV.custName' placeholder="请输入所属公司" type="" name=""></textarea></li> -->
               <!-- <li><span><em class="red">*</em> 所属公司:</span> <input id="smsContent" maxlength="50" v-model='formV.custName' placeholder="请输入所属公司" type="" name=""></li> -->
          </ul>
     </div>

          <div class="registerBtn">
                 <div class="complete-btn" @click="sun()">确认注册</div>
          </div>
	  <router-view></router-view>    
    </div>
</template>

<script type="text/javascript">
import {registeredCustomer} from '../../axiosConfig/axiosUtil'
  export default {
  name: 'Register',
  data () {
    return {
      formV: {
          phone: '' ,
          password: '' ,
          newPassword: '' ,
          name: '' ,
          custId: '' ,
      },
          phone: '' ,
          password: '' ,
          newPassword: '' ,
          name: '' ,
          custName: '' ,
   
    }
  },
  mounted(){
    var urlW = document.location.toString();
    var arrUrl = urlW.split("?");
    // console.log(urlW)
    console.log(arrUrl)
    var cust  = arrUrl[1].split("=")
    // console.log(cust)
    this.formV.custId = cust[1]
    // console.log(this.formV.custId)
  },
  computed: {

  },
  methods:{
    sun(){  
      var that = this;
      console.log(that.formV)
      console.log(this.formV.custId)
      if(!(/^1\d{10}$/.test(that.formV.phone))){
        that.$layerMsg('手机号格式不正确')
      }else{
        registeredCustomer(that.formV).then((res) => {
          if(res.data.code == 1){
            that.$layerMsg(res.data.msg)
              // window.location.href='http://solutionyun.com/registeredUserReally'
          }else{
            that.$layerMsg(res.data.msg)
            var  url = 'http://solutionyun.com/registeredUser?phone='+that.formV.phone+'&password='+that.formV.password+'&newPassword='+that.formV.newPassword+'&name='+that.formV.name+'&custId='+that.formV.custId
            var ru = encodeURIComponent(url)
            var url2 = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxf31e52205cafba8a&redirect_uri='+ ru +'&response_type=code&scope=snsapi_base&state=123#wechat_redirect'
            window.location.href=url2;
          }        
        })
      }
      
    }
  },
  created(){
    
    $(function(){ 
      $('input,textarea').on('blur',function(){
      window.scroll(0,0);
      });
      $('select').on('change',function(){
      window.scroll(0,0);
      });

      $('#smsContent').keyup(
        function(){
            var count = $(this).val().length;
            if(count > 15){
             $('#smsContent').css('line-height',"1.5rem");
          }else{
            $('#smsContent').css('line-height',"3rem");
          }
        }
    );

    })
  }
}
</script>


<style scoped>
.register{ background: #fff;
    position: relative;
    height: 100%;
    width: 100%;
}
.register ul li input{
    margin-left: 10px
}
.register ul li textarea{
    margin-left: 10px
}

</style>




