import Login from '@/components/login.vue'
import Competent from '@/components/competent/competent.vue'
import Competentindex from '@/components/competent/index/competentindex.vue'
import DutyState from '@/components/competent/index/dutyState.vue'
import OrderIndex from '@/components/competent/order/orderIndex.vue'
import MaintainIndex from '@/components/competent/index/maintainIndex.vue'
import Complete from '@/components/competent/complete/complete.vue'
import CloseOrderList from '@/components/competent/close-order-list/closeOrderList.vue'
import StayOrder from '@/components/competent/close-order-list/stayOrder.vue'
import CloseOrder from '@/components/competent/close-order/closeOrder.vue'
import CloseOrderNo from '@/components/competent/close-order/closeOrderNo.vue'
import AlreadyOrder from '@/components/competent/order/alreadyOrder.vue'
import ExamineOrder from '@/components/competent/order/examineOrder.vue'
import CompleteOrder from '@/components/competent/order/completeOrder.vue'
import RepairStatus from '@/components/competent/order/repairStatus.vue'
import RepairStatusto from '@/components/competent/order/repairStatusto.vue'
import CompleteBlcok from '@/components/competent/complete/completeBlcok.vue'
import Decice from '@/components/competent/approval/decice.vue'
import DeciceNo from '@/components/competent/approval/deciceNo.vue'
import PendingOrders from '@/components/competent/approval/pendingOrders.vue'
import AdoptOrder from '@/components/competent/approval/adoptOrder.vue'
import Reject from '@/components/competent/approval/reject.vue'
import CompleteNone from '@/components/competent/complete/completeNone.vue'
import Demand from '@/components/competent/received-order/demand.vue'
import CloseOrder02 from '@/components/competent/received-order/closeOrder02.vue'
import DemandFeedback from '@/components/competent/received-order/demandFeedback.vue'
import DemandScan from '@/components/competent/received-order/demandScan.vue'
import SceneSolve02 from '@/components/competent/received-order/sceneSolve02.vue'
import SceneSolveScan from '@/components/competent/received-order/sceneSolveScan.vue'
import SceneSolveFeedback from '@/components/competent/received-order/sceneSolveFeedback.vue'
// import SceneSolve from '@/components/competent/received-order/demandOrder.vue'
import DemandOrder from '@/components/competent/received-order/demandOrder.vue'
import Som from '@/components/competent/close-order/som.vue'

export default [
    {
      path: '/',
      name: 'Login2',
      component: Login
    },
    {
      path: '/competent',
      name: 'Competent',
      component: Competent,
		children: [
			{
			  path: 'index',
			  name: 'Competentindex',
			  component: Competentindex
			},
			{
			  path: 'closeOrder02',
			  name: 'CloseOrder02',
			  component: CloseOrder02
			},
			{
			  path: 'repairStatus',
			  name: 'RepairStatus',
			  component: RepairStatus
			},
			{
			  path: 'closeOrderNo',
			  name: 'CloseOrderNo',
			  component: CloseOrderNo
			},
			{
			  path: 'som',
			  name: 'Som',
			  component: Som
			},
			{
			  path: 'repairStatusto',
			  name: 'RepairStatusto',
			  component: RepairStatusto
			},
			{
			  path: 'sceneSolveScan',
			  name: 'SceneSolveScan',
			  component: SceneSolveScan
			},
			{
			  path: 'sceneSolveFeedback',
			  name: 'SceneSolveFeedback',
			  component: SceneSolveFeedback
			},
			{
			  path: 'demandOrder',
			  name: 'DemandOrder',
			  component: DemandOrder
			},
			{
			  path: 'demand',
			  name: 'Demand',
			  component: Demand
			},
// 			{
// 			  path: 'sceneSolve',
// 			  name: 'SceneSolve',
// 			  component: SceneSolve
// 			},
			{
			  path: 'demandFeedback',
			  name: 'DemandFeedback',
			  component: DemandFeedback
			},
			{
			  path: 'sceneSolve02',
			  name: 'SceneSolve02',
			  component: SceneSolve02
			},
			{
			  path: 'demandScan',
			  name: 'DemandScan',
			  component: DemandScan
			},
			{
				path:'dutyState',
				name:'DutyState',
				component:DutyState
			},
			{
				path:'orderIndex',
				name:'OrderIndex',
				component:OrderIndex
			},
			{
				path:'maintainIndex',
				name:'MaintainIndex',
				component:MaintainIndex
			},
			{
				path:'complete',
				name:'Complete',
				component:Complete
			},
			{
				path:'closeOrderList',
				name:'CloseOrderList',
				component:CloseOrderList
			},
			{
				path:'stayOrder',
				name:'StayOrder',
				component:StayOrder
			},
			{
				path:'closeOrder',
				name:'CloseOrder',
				component:CloseOrder
			},
			{
				path:'alreadyOrder',
				name:'AlreadyOrder',
				component:AlreadyOrder
			},
			{
				path:'examineOrder',
				name:'ExamineOrder',
				component:ExamineOrder
			},
			{
				path:'completeOrder',
				name:'CompleteOrder',
				component:CompleteOrder
			},
			{
				path:'completeBlcok',
				name:'CompleteBlcok',
				component:CompleteBlcok
			},
			{
				path:'decice',
				name:'Decice',
				component:Decice
			},
			{
				path:'deciceNo',
				name:'DeciceNo',
				component:DeciceNo
			},
			{
				path:'pendingOrders',
				name:'PendingOrders',
				component:PendingOrders
			},
			{
				path:'adoptOrder',
				name:'AdoptOrder',
				component:AdoptOrder
			},
			{
				path:'reject',
				name:'Reject',
				component:Reject
			},
			{
				path:'completeNone',
				name:'CompleteNone',
				component:CompleteNone
			}




		

		]
	}
]
		 



