<template>
	<div>
  <div class="login">
    <div class="login-img"><img src="static/img/login.png"></div>
  
      <form class="login-message">
      <ul>
        <li><span class="iconfont icon-geren"></span> <input v-model='formV.userName' placeholder="请输入用户名" type="text" name=""></li>
        <li><span class="iconfont icon-mima"></span> <input v-model='formV.password' placeholder="请输入密码" type="password" name=""></li>		
      </ul>

    <!--  <div class="radio"> 
      <div><input  type="radio" class="radio-style" checked="" name="radio">  <label>客户</label></div>
      <div><input  type="radio" class="radio-style" name="radio"> 
       个人</div>
      </div> -->
       <a class="submit" @click='loginFn()'>登录</a>
      </form>
      
     </div>
     <div style="width: 100%; height:100%;z-index: 1; position: fixed; bottom: 0; background: #009b82" > </div>
  </div>
</template>

<script>
	import {getCode,WeChatDologin} from '../axiosConfig/axiosUtil'
	import { mapGetters } from 'vuex'
export default {
  name: 'Login2',
  data () {
    return {
			srcc: '',
			formV: {
				userName: '',
				password: '',
				code: ''
			}
    }
  },
	mounted(){
		// localStorage.removeItem('openId')
		// this.$store.dispatch('user')
	},
	computed: {
		...mapGetters({
			id : 'user'
		}),
		
	},
	methods:{
		loginFn(){	
   		this.$layerMsg("注册成功")
		this.$router.push({path:'/'})
		}
	},
	created(){
		this.loginFn()
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login{height:100%;position: relative;z-index: 99}
</style>



