<template>
  <div>
	<div class="top"></div>

     <div class="nav-fixed">
          <div class="nav">
          	<a @click="$router.back(-1)" class="iconfont icon-fanhui"></a>
          		<div>维修反馈</div>
          		<a></a> 
          </div>
     </div>



          <div class="feedback-box" style="margin-top:1.25rem">
           <h1>处理措施</h1>
          <textarea v-model='treatmentMeasure' placeholder="请输入处理措施" maxlength="500"></textarea>
         </div>



          <div class="footer-none"></div>
          <div class="bottom">
               <div v-on:click="alreadyorder()" class="complete-btn">服务完成</div>
          </div>

</div>
</template>

<script>
	import{overOrderAccident,overOrderOther} from '../../../axiosConfig/axiosUtil'
    export default {
      name: 'index',     
      data () {
			return {
					formV: '',
					treatmentMeasure: ''
				}
			},
     methods:{
			alreadyorder() {
				var that = this
				// console.log(that.formV)
				that.formV.treatmentMeasure = that.treatmentMeasure 
				overOrderOther(that.formV)
				.then((res) => {
					if(res.data.code == 0){
						that.$layerMsg(res.data.msg)
						that.$router.push({path:'/competent/alreadyorder'})
					}else{
						that.$layerMsg(res.data.msg)
					}
				})
				
			},
		getData(){
			var that = this
			that.formV = JSON.parse(sessionStorage.getItem('alreadyorderV'))
		},
    


  }, 
   created(){       
		this.getData()
  },


}

</script>

<style scoped>

</style>



