<template>
  <div> 

        <div class="nav-fixed">
          <div class="nav">
            <a @click="$router.back(-1)" class="iconfont icon-fanhui"></a>
              <div>评价</div>
              <p></p> 
          </div>
        </div>
   
     <div class="top"></div>
	   <div class="service">
       <span>服务评价</span> 
       <a>满意请给五星好评哦</a>
     </div>
      <div class="service-start">
       服务满意度:
       <div  v-for='(item,index) in 5' class="iconfont icon-xingxing1" @click="getIndex(index)" :class="{'yellow': statevalue > index }"></div>
      </div>  
     <div style="text-align: center; font-size: 1.875rem;color: #666666;background: #fff;padding: 1rem 0">{{formV.cust}}</div>
	    <div class="service-opinion">
         <textarea maxlength="100" v-model="formV.custEva" placeholder="本次服务是否满足您的需求? 评价一下维修员,让他们越做越好!"></textarea>
      </div>

          <div class="repairbtn">
               <div  class="repair-btn" @click='subMit()'>提 交</div>
          </div>

    <div class="category" v-show="isShowbx">
      <div class="evaluate-order">
        <h1><span class="iconfont icon-zhengque"></span></h1>
        <div class="success">评价成功</div>
        <div class="success-see">可以在报修单详情里进行查看哦！</div>
        <span v-on:click="state" class="evaluate-btn">我知道了</span>

      </div>
    </div>


		<!-- 底部 -->
          <div class="footer-none"></div>


</div>
</template>

<script>
import {doAddCustSatisfa} from '../../../axiosConfig/axiosUtil'
export default {
      name: 'Customerindx',   
  data () {
    return {
         formV: {
              woNumber: '' ,
              custScore: '' ,
              custEva: '' ,
                 cust: '满意！' ,
            },
          isShowbx: false,
          statevalue: 4 ,


    }
  },
     methods:{
           getIndex(index){  
              this.woNumber=this.$route.query.name;
            if(index==0){
              this.statevalue=1;
              this.formV.cust="非常不满意！"
            }
             if(index==1){
              this.statevalue=2;
               this.formV.cust="不满意！"
            }
             if(index==2){
              this.statevalue=3;
               this.formV.cust="一般！"
            }
             if(index==3){
              this.statevalue=4;
               this.formV.cust="满意！"
            }
             if(index==4){
              this.statevalue=5;
               this.formV.cust="非常满意！"
            }
        },
    customerindex: function () {
      this.$router.push({path:'/customer/customerindex'})
    },
     state: function () { 
    this.$router.push({path:'/customer/repairStatus',query:{name:'已完成'}})
    },   
    subMit(){ 
      var that = this
      // console.log(that.formV);
      that.formV.custScore = this.statevalue
      that.formV.woNumber=this.woNumber
      doAddCustSatisfa(that.formV)
      .then((res) => {
          if(res.data.code == 1){
            that.$layerMsg(res.data.data);
            that.isShowbx = false;
            this.$router.push({path:'/customer/repairIndexComplete'})
        // console.log(res)



      }else{
      that.isShowbx = true;

      }

      })
    },

  
  }, 
   created(){
       this.getIndex();
       var statevalue=this.$route.query.name; 
       $(function(){
     $('input,textarea').on('blur',function(){
      window.scroll(0,0);
      });
      $('select').on('change',function(){
      window.scroll(0,0);
      });
       })

  },


}

</script>


<style scoped>

</style>



