<template>
  <div>
    

	<div class="top"></div>
     <div class="nav-fixed">
          <div class="nav">
          	<a @click="$router.back(-1)" class="iconfont icon-fanhui"></a>
          		<div>填写保养信息</div>
          		<a></a> 
          </div>
     </div>


     <div class="scan-code">
     	<div>机器编码</div>
     	<a> <input style="text-align: right; background: #fff" type="" v-model='formVC.machCode' disabled  placeholder="请输入机器编码" name=""></a>
     </div>

     <div class="accident-list">
     <div class="details-box">
      	<ul>
      		<li><span>设备名称</span><span>{{ formV.devName }}</span></li>
      		<li><span>设备型号</span><span>{{ formV.unitType }}</span></li>
      		<li><span>设备位置</span><span>{{ formV.location }}</span></li>
      		<li><span>客户名称</span><span>{{ formV.custArea }}</span></li>
      		<li><span>部门</span><span>{{ formV.department }}</span></li>
      	</ul>
      </div>

         <div class="details-box" style="margin-top: 2.5rem">
          <ul>
               <li><span><em class="red">*</em> 黑白读数</span> <input v-model.trim='formVC.bwReader' id="ppp" type="" name="" placeholder="请输入黑白读数"></li>
               <li><span><em class="red">*</em> 彩色读数</span> <input v-model.trim='formVC.coReader' type="" name="" placeholder="请输入彩色读数"></li>
               <li><span><em class="red">*</em> 耗材型号</span><input v-model.trim='formVC.materialModel' title="" type="" name="" placeholder="请输入耗材型号"></li>
               <li><span><em class="red">*</em> 耗材数量</span><input  v-model.trim='formVC.materialNumber' title="" type="" name="" placeholder="请输入耗材数量"></li>
              <li><span><em class="red" style="visibility: hidden;">*</em> 覆盖率</span><input v-model='formVC.coverage' title="" type="" name="" placeholder="请输入覆盖率"></li>
              </ul>
         </div>
      <div class="footer-none"></div>
 		<div class="bottom">
 			<div class="repair-btn" @click='subMit()'>服务完成</div>
 		</div>
 </div>


 </div>
</template>		

<script type="text/javascript">
 import {customerScannerOrder} from '../../../axiosConfig/axiosUtil'
import {EngMaintenancePerform} from '../../../axiosConfig/axiosUtil'
import {maintenancePlanByMachCode} from '../../../axiosConfig/axiosUtil'
  export default {
      name: 'ScanMaintain',   
  data () {
    return {
        formV: {
          machCode: '' ,
          bwReader: '',
          coReader: '',
          materialModel: '',
          materialNumber: '',
          coverage: ' ',
        },
        formVC: {
          machCode: '' ,
        },
        category: ['软件故障','硬件故障','耗材供断']
    }
  },
     methods:{
    index: function () {
      this.$router.push({path:'/engineer/index'})
    },
     isShowtk: function () {
       this.isshowtk = true
    },
     getData(){
      var that = this 
      var statevalue=this.$route.query.sbb;
      this.formV.machCode=statevalue;
      this.formVC.machCode=this.formV.machCode
      console.log(that.formV)
      maintenancePlanByMachCode(that.formV)
      .then((res) => { 
        that.formV=res.data.data;
        console.log(res)
       
      })
  },

    subMit(){
      var that = this 
      console.log(that.formVC)
      EngMaintenancePerform(that.formVC)
      .then((res) => { 
        console.log(res)
         if(res.data.code == 1){
             that.$layerMsg(res.data.msg)
        }else{ 
             that.$layerMsg("保养成功")
             that.$router.push({path:'/engineer/index'})
        }

      })
  },
  }, 
   created(){
    this.getData()  
    var statevalue=this.$route.query.sbb;
    this.indexUrl = localStorage.getItem('openId');
    $(function(){
       $('input,textarea').on('blur',function(){
        window.scroll(0,0);
      });
      $('select').on('change',function(){
        window.scroll(0,0);
      });
    })

  },


}

</script>


<style scoped>

</style>



