<template>
<!-- 已关单 -->
  <div>
  
     <div class="nav-fixed">
          <div class="nav">
             工单
          </div>
     </div>
     <div class="top"></div>


<!-- 工单 -->
			<div class="receipt">
				<router-link class="receipt-color" to='/competent/orderIndex'>已关单</router-link>
				<router-link to='/competent/alreadyOrder'>已接单</router-link>
				<router-link to='/competent/examineOrder'>审批单</router-link>
				<router-link to='/competent/completeOrder'>已完成</router-link>
			</div>

        <!--   已关单 -->
            <a @click='details(list)' v-for='list in items'>
               <div class="demand-box">
                 <div class="demand-time">
                      <span>{{ list.repairTime }}</span>
                 </div>
                 <div class="demand-box1">
                      <div class="demand-box2">
                      <h1 class="iconfont icon-shebei1"><a>{{ list.woNumber }}</a></h1>
                      </div>
                      <h2 class="demand-name">客户名称:<span>{{ list.custName }}</span></h2>
                       <h2 class="demand-name-1">服务类型:<span>{{ list.faultType }}</span></h2>
                      <div class="demand-box3">
                      <h1 class="demand-name">{{ list.dd }}:<span>{{ list.describe }}</span></h1>
                      <a>查看详情>></a>
                      </div>
                 </div>
               </div>
            </a>
            
            <div v-show='showgd' style="text-align: center; font-size: 2.5rem;padding: 1.5rem 0;color: #666666">暂无工单！</div>

<!--   <div><span><a href="tel:13667391979">拨号</a></span></div> -->

		<!-- 底部 -->
          <div class="footer-none"></div>
	 <footer class="footer1">
    <footer class="footer">
    	<section class="on">
    		<router-link to="/competent/index">
    			<i class="iconfont icon-shouye"></i>
    			<div>首页</div>
    		</router-link>			    
    	</section>
    	<section>
    		<router-link to="/competent/orderIndex">
    			<i  style='color:#7898ff' class="iconfont icon-gongdan1"></i>
    			<div style='color:#7898ff'>工单查询</div>
    		</router-link>
    	
    	</section>
    	<section>
    		<router-link to="/competent/maintainIndex">
    			<i class="iconfont icon-_shebeibaoyang"></i>
    			<div>保养查看</div>
    		</router-link>
    	
    	</section>
    	<section>
    		<router-link to="/competent/dutyState">
    			<i class="iconfont icon-yuangongguanli"></i>
    			<div>在岗状态</div>
    		</router-link>			
    	</section>
    </footer>
  </footer>

</div>
</template>

<script>
import {TechnicalNotOverOrder} from '../../../axiosConfig/axiosUtil'		
export default {
	name: 'OrderIndex',       
    data () {
		return {
			formV: {
				woStatus: '3'
			},
			items: '',
      showgd: '',
      dd: '',
		}
	},
    methods:{
		getData(){
			var that = this
			TechnicalNotOverOrder(that.formV)
			.then((res) => {
				if(res.data.code == 0){
          if(res.data.data==''){
              this.showgd=true;
            }
					// res.data.data.repairTime = that.$date(res.data.data.repairTime)
					for(var i = 0,l = res.data.data.length;i<l;i++){
						res.data.data[i].repairTime = that.$date(res.data.data[i].repairTime)
            if(res.data.data[i].faultType == '事故类'){
              res.data.data[i].dd = '故障描述'
              // that.dd == '故障描述'
            }else{
              // alert(2)
              res.data.data[i].dd = '需求描述'
              // that.dd == '需求描述'
            }
						for(var v in res.data.data[i]){
							if(!res.data.data[i][v]){
								res.data.data[i][v] = '暂无'
							}
						}
					}
					that.items = res.data.data					
				}
			})
		},
		details(str){
			var that = this
			sessionStorage.setItem('orderIndexV',JSON.stringify(str))
      if(str.faultType == "事故类"){
        that.$router.push({path: '/competent/completeBlcok'})
      }else{
        that.$router.push({path: '/competent/demandOrder'})
      }
			
		}
	}, 
	created(){
		this.getData()
	},
}

</script>


<style scoped>

</style>

