<template>
  <div>
     <div class="nav-fixed">
          <div class="nav">
             意见反馈
          </div>
     </div>
     <div class="top"></div>
	
	<div class="opinion">
         <textarea   v-model="formV.content" placeholder="您的意见是我们的动力！"></textarea>
         </div>
          <div class="repairbtn">
               <div  @click="subMit()" class="repair-btn">提 交</div>
          </div>

	<!-- 	底部 -->
          <div class="footer-none"></div>
	<footer class="footer1">
		<footer class="footer">
			<section v-on:click="index">
			<i class="iconfont icon-shouye"></i>
			<div>首页</div>
			</section>
			<section  v-on:click="repairIndex">
			<i class="iconfont icon-baoxiu"></i>
			<div>报修记录</div>
			</section>
			<section class="on" v-on:click="feedbackindex">
			<i class="iconfont icon-yijianfankui"></i>
			<div>意见反馈</div>
			</section>
		</footer>
	</footer>


</div>
</template>
<script>
  import {customerDoAdvice} from '../../../axiosConfig/axiosUtil'
    export default {
      name: 'Feedbackindex',   
    
      data () {
    return {
        formV: {
          content: '',
          openId:'',
        }
    }
  },
     methods:{
        subMit(){   
      var that = this; 
      // console.log(that.formV);
     
        customerDoAdvice(that.formV)
        .then((res) => { 
              //  console.log(res)
               if(res.data.code == 0){  
                that.formV.openId = this.indexUrl;
                this.openId=that.formV.openId;
                that.$layerMsg(res.data.msg) 
                this.$router.push({path:'/customer/index'})
               }

      }) 
    },
    repairIndex: function () {
      this.$router.push({path:'/customer/repairIndex'})
    },
    index: function () {
      this.$router.push({path:'/customer/index'})
    },
    feedbackindex: function () {
      this.$router.push({path:'/customer/feedbackindex'})
    },
    customerindex: function () {
      this.$router.push({path:'/customer/index'})
    }


  }, 
   created(){
    this.indexUrl = localStorage.getItem('openId');
  },


}

</script>


<style scoped>

</style>






