<template>
  <div>
     <div class="nav-fixed">
          <div class="nav">
             首页
          </div>
     </div>
     <div class="top"></div>
	<!-- 首页 -->
	<div class="img"></div>
	<!-- 报修 -->
	<div class="box">     
      <div class="repair_1">
        <div class="repair">        
          <section>
              <a @click="sys_click()">
             <i class="iconfont icon-weihubaoyang icon-shiguleixingtongji_1"></i>
          <div>扫一扫保养</div>
              </a>
          </section>

          <section>
             <a @click="sys_click02()">
          <i class="iconfont icon-saoyisao1 icon-shiguleixingtongji_2"></i>
               <div>扫一扫报修</div>
            </a>
          </section>      
        </div>
      </div>
    </div>
		<div class="competent-scheme">
			<router-link to='/competent/closeOrderList'>
					<div class="scheme-title">
						<div>待关单</div>
						<a>查看更多>></a>
					</div>
			</router-link>
			<a v-for="list in itemsA" @click='detailsA(list)'>
			    <div class="competent-box" id="closing-order">
			    <div class="competent-box1">
			         <div class="competent-box2">
			         <h1 class="iconfont icon-shebei1"><a>{{ list.woNumber }}</a></h1>
			         </div>
			         <h2 class="competent-name">客户名称:<span>{{ list.custName }}</span></h2>
			          <h2 class="competent-name-1">{{aa}}:<span>{{ list.describe }}</span></h2>
			         <div class="competent-box3">
			         <h1 class="competent-name">报修时间:<span>{{ list.repairTime }}</span></h1>
			         <a>查看详情>></a>
			         </div>
			    </div>
			    </div>
			</a>

			<div v-show='showgd' style="text-align: center; font-size: 2.5rem;padding: 1.5rem 0;color: #666666">暂无工单！</div>
		</div>  



  <div class="competent-scheme">
      <router-link to='/competent/stayOrder'>
        <div class="scheme-title">
            <div>待接单</div>
            <a>查看更多>></a>
        </div>
      </router-link>

          <a v-for="list2 in itemsB" @click='detailsB(list2,"/competent/complete")'>
            <div class="competent-box">
               <div class="competent-box1">
                    <div class="competent-box2">
                    <h1 class="iconfont icon-shebei1"><a>{{ list2.woNumber }}</a></h1>
                    </div>
                    <h2 class="competent-name">客户名称:<span>{{ list2.custName }}</span></h2>
                     <h2 class="competent-name-1">{{dd}}:<span>{{ list2.describe }}</span></h2>
                    <div class="competent-box3">
                    <h1 class="competent-name">报修时间:<span>{{ list2.repairTime }}</span></h1>
                    <a>查看详情>></a>
                    </div>
               </div>
            </div>
          </a>

          <div v-show='showgdx' style="text-align: center; font-size: 2.5rem;padding: 1.5rem 0;color: #666666">暂无工单！</div>
          
    </div>

		<!-- SOM操作说明 -->
		<div class="competent-scheme">
			<router-link to='/competent/som' class="scheme-title">
					<div>SOM操作说明</div>
					<a>查看更多>></a>
			</router-link>
		</div>	


		<!-- 底部 -->
          <div class="footer-none"></div>
	<footer class="footer1">
		<footer class="footer">
			<section class="on">
				<router-link to="/competent/index">
					<i  style='color:#7898ff' class="iconfont icon-shouye"></i>
					<div style='color:#7898ff'>首页</div>
				</router-link>			    
			</section>
			<section>
				<router-link to="/competent/orderIndex">
					<i class="iconfont icon-gongdan1"></i>
					<div>工单查询</div>
				</router-link>
			
			</section>
			<section>
				<router-link to="/competent/maintainIndex">
					<i class="iconfont icon-_shebeibaoyang"></i>
					<div>保养查看</div>
				</router-link>
			
			</section>
			<section>
				<router-link to="/competent/dutyState">
					<i class="iconfont icon-yuangongguanli"></i>
					<div>在岗状态</div>
				</router-link>			
			</section>
		</footer>
	</footer>



</div>
</template>


<script>
import wx from 'weixin-js-sdk'
import {TechnicalNotOverOrder} from '../../../axiosConfig/axiosUtil'
export default {
	name: 'Competentindex',       
    data () {
		return {
			formV: {
				woStatus: '1' //待关单
			},			
			itemsA: '', //带接单数据
			itemsB: '' , //待关单数据
			showgd:false,
			showgdx:false,
			dd:'',
			aa:'',
		}
	},
    methods: {
		// 待关单
		getData1(){
			var that = this
			that.formV.woStatus = 1
			TechnicalNotOverOrder(that.formV)
			.then((res) => {
			if(res.data.data==''){
	            this.showgd=true;
	          }
				if(res.data.code == 0){
					// res.data.data.repairTime = that.$date(res.data.data.repairTime)
					for(var i = 0,l = res.data.data.length;i<l;i++){
						res.data.data[i].repairTime = that.$date(res.data.data[i].repairTime)

					if(res.data.data[i].faultType == '事故类'){
						that.aa='故障描述'
					}else{
						that.aa='需求描述'
					}
							
					
						
						for(var v in res.data.data[i]){
							if(!res.data.data[i][v]){
								res.data.data[i][v] = '暂无'
							}
						}
					}
					that.itemsA = res.data.data
					sessionStorage.setItem('closeOrderV',JSON.stringify(that.itemsA))
					that.getData2()
				}
			})			
		},
		//待接单
		getData2(){
			var that = this
			that.formV.woStatus = 2
			TechnicalNotOverOrder(that.formV)
			.then((res) => {
			if(res.data.data==''){
	            this.showgdx=true;
	          }
				if(res.data.code == 0){
					// res.data.data.repairTime = that.$date(res.data.data.repairTime)
					for(var i = 0,l = res.data.data.length;i<l;i++){
						res.data.data[i].repairTime = that.$date(res.data.data[i].repairTime)
						if(res.data.data[i].faultType == '事故类'){
						that.dd='故障描述'
					}else{
						that.dd='需求描述'
					}
						for(var v in res.data.data[i]){
							if(!res.data.data[i][v]){
								res.data.data[i][v] = '暂无'
							}
						}
					}
					that.itemsB = res.data.data
					sessionStorage.setItem('completeV',JSON.stringify(that.itemsB))
				}
			})
		},
		detailsA(str,stra){
			var that = this
			sessionStorage.setItem('closeOrderListV',JSON.stringify(str))
			if(str.faultType == '事故类'){
				that.$router.push({path: '/competent/closeOrder'})
			}else{
				that.$router.push({path: '/competent/closeOrderNo'})
			}		
			that.$router.push({path: stra})
		},
		detailsB(str,stra){
			var that = this
			sessionStorage.setItem('stayOrderV',JSON.stringify(str))			
			that.$router.push({path: stra})
		},
		sys_click(){ 
        var that = this;
        var url=location.href;
        $.get("http://47.106.110.201:80/som/getSign?url="+url, function(data){    
            wx.config({
                // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                debug: false,
                // 必填，公众号的唯一标识
                appId: data.appId,
                // 必填，生成签名的时间戳
                timestamp: "" + data.timestamp,
                // 必填，生成签名的随机串
                nonceStr: data.noncestr,
                // 必填，签名
                signature: data.signature,
                // 必填，需要使用的JS接口列表，所有JS接口列表
                jsApiList: ['checkJsApi', 'scanQRCode']
            });

        });
        wx.error(function (data) {
            alert("出错了：" + data.errMsg);//这个地方的好处就是wx.config配置错误，会弹出窗口哪里错误，然后根据微信文档查询即可。
        });
 
        wx.ready(function () {
            wx.checkJsApi({
                jsApiList: ['scanQRCode'],
                success: function (data) {
                }
            });
			wx.scanQRCode({
				needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
				scanType: ["qrCode"], // 可以指定扫二维码还是一维码，默认二者都有
				success: function (data) {
					var result = data.resultStr; // 当needResult 为 1 时，扫码返回的结果
					var ac = result.split('&')
					var icc = 8
					var acc = JSON.stringify(ac[1])
					// alert("扫描结果："+result);
					// alert("acc"+acc);
					// that.$layerMsg(result)
					var vals = acc.substring(68,acc.length-1)
				    if(vals){
					  // alert(vals)
					  that.$router.push({path: '/engineer/scanMaintain',query:{sbb: vals}}),
					  location.reload()
					}else{
					  // alert(1)
					  window.location.href = result;//因为我这边是扫描后有个链接，然后跳转到该页面
					}
					
				}
			}); 
        }); 
        },
		sys_click02(){ 
        var that = this ;
         var url=location.href;
        $.get("http://47.106.110.201:80/som/getSign?url="+url, function(data){    
            wx.config({
                // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                debug: false,
                // 必填，公众号的唯一标识
                appId: data.appId,
                // 必填，生成签名的时间戳
                timestamp: "" + data.timestamp,
                // 必填，生成签名的随机串
                nonceStr: data.noncestr,
                // 必填，签名
                signature: data.signature,
                // 必填，需要使用的JS接口列表，所有JS接口列表
                jsApiList: ['checkJsApi', 'scanQRCode']
            });

        });
        wx.error(function (data) {
            alert("出错了：" + data.errMsg);//这个地方的好处就是wx.config配置错误，会弹出窗口哪里错误，然后根据微信文档查询即可。
        });
 
        wx.ready(function () {
            wx.checkJsApi({
                jsApiList: ['scanQRCode'],
                success: function (data) {
                }
            });
                wx.scanQRCode({
                    needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
                    scanType: ["qrCode"], // 可以指定扫二维码还是一维码，默认二者都有
                    success: function (data) {
                        var result = data.resultStr; // 当needResult 为 1 时，扫码返回的结果
                        var ac = result.split('&')
                        var icc = 8
                        var acc = JSON.stringify(ac[1])                        
                        // alert("扫描结果："+result);
                        var vals = acc.substring(68,acc.length-1)
                        if(vals){
                          // alert(vals)
                          that.$router.push({path: '/engineer/scan',query:{sbb: vals}}),
						  location.reload()
						  
                        }else{
                          // alert(1)
                        }
                        // window.location.href = result;//因为我这边是扫描后有个链接，然后跳转到该页面
                    }
                });
 
        }); 
        },
   	}, 
	created() {
		this.getData1()
		this.getData2()
	}
}

</script>


<style scoped>

</style>


