<template>
  <div>
     <div class="nav-fixed">
          <div class="nav">
           <a @click="$router.back(-1)" class="iconfont icon-fanhui"></a> 
              <div>保养信息</div>
            <span @click="toggle()" class="iconfont icon-shaixuan" id="screen"></span> 
          </div> 

      <!--           隐藏域 -->
    <div  class="qipao-box" v-if="isShow">
        <div class="qipao"  v-for='(listA,index) in item' @click='edit(listA,index)'>
          <div v-bind:class='{bg:index==isactive}'>
                {{ listA }}
            </div>
        </div><div class="demo"></div>
    </div>
    <!--           隐藏域 -->

     </div>  <div class="top"></div>
      <div class="receipt">
        
        <div @click="deciceNo()" class="receipt-color">未完成</div>
		<div id="etc" @click="decice()" >已完成</div>
      </div>

      <div v-show='showgd' style="text-align: center; font-size: 2.5rem;padding: 1.5rem 0;color: #666666;" >暂无信息！</div>
			<!--  --- -->
			<div class="decice-for" v-for='list in items'>
						<div class="decice-for-box">
							<span>{{list.machCode}}</span>
							<span class="decice-red">未完成</span>
						</div>
						<ul>
							<li><span>设备名称:</span><span>{{ list.devName }}</span></li>
							<li><span>黑白读数:</span><span>{{ list.bwReader }}</span></li>
							<li><span>彩色读数:</span><span>{{ list.colorReader }}</span></li>
							<li><span>保养频率</span><span>{{ list.mainFrequency }}</span></li>
							<li><span>设备位置</span><span>{{ list.localtion }}</span></li>
						</ul>
			</div>

</div>
</template>

<script>
import {TechnicalMaintenancePlan,EngMaintenancePlan} from '../../../axiosConfig/axiosUtil'
export default {
	name: 'Decice',
	data () {
    return {
			item: ['周','月','季度','半年','年'],
			isShow: false,
			isactive: 0,
			showgd: false,
			formV: '',
			items: ''
    }
  },
  methods:{   
		decice: function () {
		  this.$router.push({path:'/competent/decice'})
		},
		 deciceNo: function () {
		  this.$router.push({path:'/competent/deciceNo'})
		},
		getData(){
			var that = this
			that.formV = JSON.parse(sessionStorage.getItem('index03V'))
			that.formV.Cycle = ''
			that.formV.isOver = '未完成'
			if(that.formV.myMain){
				EngMaintenancePlan(that.formV)
				.then((res) => {
					if(res.data.data==''){
								that.showgd=true;
							}else{
								that.showgd=false;
							}
					if(res.data.code == 0){
						that.items = res.data.data
					}else{
						that.$layerMsg(res.data.msg)
					}
				})
			}else{
				TechnicalMaintenancePlan(that.formV)
				.then((res) => {
					if(res.data.data==''){
								that.showgd=true;
							}else{
								that.showgd=false;
							}
					if(res.data.code == 0){
						that.items = res.data.data
					}else{
						that.$layerMsg(res.data.msg)
					}
				})
			}
			
		},
		edit(str,index){
			 this.isactive=index;
			var that = this
			that.isShow = false
			that.formV.Cycle = str
			that.formV.isOver = '未完成'
			if(that.formV.myMain){
				EngMaintenancePlan(that.formV)
				.then((res) => {
					if(res.data.data==''){
								that.showgd=true;
							}else{
								that.showgd=false;
							}
					if(res.data.code == 0){
						that.items = res.data.data
					}else{
						that.$layerMsg(res.data.msg)
					}
				})
			}else{
				TechnicalMaintenancePlan(that.formV)
				.then((res) => {
					if(res.data.data==''){
								that.showgd=true;
							}else{
								that.showgd=false;
							}
					if(res.data.code == 0){
						that.items = res.data.data
					}else{
						that.$layerMsg(res.data.msg)
					}
				})
			}
		},
		toggle(){
			var that = this
			if(that.isShow == false){
				that.isShow = true
			}else if(that.isShow == true){
				that.isShow = false
			}
		}
  },   
  created(){
    this.getData()
  },


}

</script>


<style scoped>

</style>



