<template>
<div>
     <div class="nav-fixed">
          <div class="nav">
            <div>保养计划</div>    
          </div>

     </div>

     <div class="top"></div>

     <!-- 保养计划 -->
		<div class="topTab">
			<div class="tabCom" :style="{color: color }" @click="my()">我的保养</div>
			<div class="tabEng" :style="{color: colorE }" @click="all()">所有保养</div>
		</div>
		<div v-if="myMaintain">
			<div class="maintain-frame">            
				<div class="maintain" @click='details(list)'  v-for='list in formV' id="maintain">
					<div class="maintain-one">
					<h1>合同编号:<span>{{ list.contractNo }}</span></h1>
					<h1>客户名称:<span>{{ list.custName }}</span></h1>
					<!-- <h1>保养频率:<span>月</span></h1> -->
					</div>
					<a class="iconfont icon-chakanxiangqing"></a>
				</div>
			</div>
			<div v-show='showmy' style="text-align: center; font-size: 2.5rem;padding: 1.5rem 0;color: #666666">暂无保养计划数据！</div>
		</div>
		<div v-else>
			<div class="maintain-frame">            
				<div class="maintain" @click='details(list)'  v-for='list in formV' id="maintain">
					<div class="maintain-one">
					<h1>合同编号:<span>{{ list.contractNo }}</span></h1>
					<h1>客户名称:<span>{{ list.custName }}</span></h1>
					<!-- <h1>保养频率:<span>月</span></h1> -->
					</div>
					<a class="iconfont icon-chakanxiangqing"></a>
				</div>
			</div>
			<div v-show='showgd' style="text-align: center; font-size: 2.5rem;padding: 1.5rem 0;color: #666666">暂无保养计划数据！</div>
		</div>
		

     <!-- 保养计划 -->
<!-- 底部 -->
          <div class="footer-none"></div>
  <footer class="footer1">
    <footer class="footer">
    	<section class="on">
    		<router-link to="/competent/index">
    			<i class="iconfont icon-shouye"></i>
    			<div>首页</div>
    		</router-link>			    
    	</section>
    	<section>
    		<router-link to="/competent/orderIndex">
    			<i class="iconfont icon-gongdan1"></i>
    			<div>工单查询</div>
    		</router-link>
    	
    	</section>
    	<section>
    		<router-link to="/competent/maintainIndex">
    			<i  style='color:#7898ff' class="iconfont icon-_shebeibaoyang"></i>
    			<div style='color:#7898ff'>保养查看</div>
    		</router-link>
    	
    	</section>
    	<section>
    		<router-link to="/competent/dutyState">
    			<i class="iconfont icon-yuangongguanli"></i>
    			<div>在岗状态</div>
    		</router-link>			
    	</section>
    </footer>
  </footer>

</div>
</template>

<script>
import {TechnicalMaintenanceContract,EngMaintenanceContract} from '../../../axiosConfig/axiosUtil'	
export default {
	name: 'MaintainIndex',   
	data () {
		return {
			formV: '',
			// formMy: '',
      		showgd: '',
			showmy:'',
			color : '#7598ff',
			colorE: '#000',
			myMaintain: true,
			// all: false,
		}
	},
    methods:{
		getData(){
			var that = this
			//全部
			
			// 我的
			// var userId = localStorage.getItem('openId')
			EngMaintenanceContract()
			.then((res) => {
        		if(res.data.data==''){
					this.showmy=true;
				}
				if(res.data.code == 0){
					// console.log(res.data.data)
					var data = res.data.data
					that.formV = data
					// console.log(that.formV)
					for(var i=0; i<data.length; i++){
						that.formV[i].myMain = 'my'
					}
					// console.log(that.formV,'mainMy')
				}
			})
		},
		details(str){
			var that = this
			sessionStorage.setItem('index03V',JSON.stringify(str))
			that.$router.push({path: '/competent/deciceNo'})
		},
		// detailsMy(str){
		// 	var that = this
		// 	sessionStorage.setItem('index03M',JSON.stringify(str))
		// 	that.$router.push({path: '/competent/decice'})
		// },
		//我的保养
		my(){
			var that = this;
			that.color = '#7598ff'
			that.colorE = '#000'
			that.myMaintain = true
			that.formV = ''
			EngMaintenanceContract()
			.then((res) => {
        		if(res.data.data==''){
					this.showmy=true;
				}
				if(res.data.code == 0){
					// console.log(res.data.data)
					var data = res.data.data
					that.formV = data
					// console.log(that.formV)
					for(var i=0; i<data.length; i++){
						that.formV[i].myMain = 'my'
					}
					// console.log(that.formV,'mainMy')
				}
			})
		},
		//全部保养
		all(){
			var that = this;
			that.color = '#000'
			that.colorE = '#7598ff'
			that.myMaintain = false
			that.formV = ''
			TechnicalMaintenanceContract()
			.then((res) => {
        		if(res.data.data==''){
					this.showgd=true;
				}
				if(res.data.code == 0){
					that.formV = res.data.data
					// console.log(that.formV,'mainAll')
				}
			})
		}
	}, 
	created(){
		this.getData()
	},
}

</script>


<style scoped>
.topTab{
	width: 100%;
    height: 40px;
	line-height: 40px;
	background: #fff;
}
.topTab>div{
	width: 50%;
	float: left;
	text-align: center;
	font-size: 2rem;
}
.topTab>div:hover{
	cursor: pointer;
	color: #7598ff;
}
.pointFont{
	color: #7598ff;
}
</style>


