      var i= $(this).index(); 
         if(i==0){
           window.location.href="competent-index.html";
      }
       if(i==1){
             window.location.href="order/order-index.html";
      }
       if(i==2){ window.location.href="maintain-index.html";
      }
       if(i==3){ window.location.href="duty-state.html";
      }
    
})


