<template>
  <div>
     <div class="nav-fixed">
          <div class="nav">
             工单
          </div>
     </div>
     <div class="top"></div>


<!-- 工单 -->
		<div class="work-order">
			<div class="receipt">
				<div class="receipt-color" id="etc"><router-link style='width:100%; color: #7598ff' to='/engineer/alreadyorder'>已接单</router-link></div>
        <div class="" id=""><router-link style='width:100%' to='/engineer/examineOrder'>待订件</router-link></div>
				<div class="" id=""><router-link style='width:100%' to='/engineer/completeOrder'>已完成</router-link></div>
				<div class="" id=""><router-link style='width:100%' to='/engineer/catchSheet'>已转单</router-link></div>
			</div>
      </div>

      <!--   已接单 -->
               <div class="received">
                  <a @click='details(list)' v-for='list in items'>
										<div> 
										   <div class="received-box" id="event">
										       <div class="received-time">
										            <span>{{ list.repairTime}}</span>
										       </div>
										       <div class="received-box1">
										            <div class="received-box2">
										            <h1 class="iconfont icon-shebei1"><a>{{ list.woNumber }}</a></h1>
										            <span>{{ list.woState}}</span>
										            </div>
										            <h2 class="received-name">客户名称:<span>{{ list.custName}}</span></h2>
										            <h2 class="event-name-1">服务类型:<span>{{ list.faultType}}</span></h2>
										            <div class="received-box3">
										            <h1 class="received-name">{{ list.dd }}:<span>{{ list.describe}}</span></h1>
										            <a>查看详情>></a>
										            </div>
										       </div>
										  </div>
										</div>
									</a>
									<div v-show='showgd' style="text-align: center; font-size: 2.5rem;padding: 1.5rem 0;color: #666666">暂无工单！</div>
							 </div>

  			

		<!-- 底部 -->
<div class="footer-none"></div>
		  <footer class="footer1">
		    <footer class="footer">
		      <section  class="">
				<router-link  :to='{path: "/engineer/index"}'>
					<i class="iconfont icon-shouye"></i>
					<div>首页</div>
				</router-link>      
		      </section>
			<section class=''>
				<router-link  :to='{path: "/engineer/alreadyorder"}'>
					<i  style='color:#7898ff' class="iconfont icon-gongdan"></i>
					<div style='color:#7898ff'>工单</div>				
				</router-link>
			</section>      
			<section class='on'>
				<router-link :to='{path: "/engineer/index03"}'>
					<i class="iconfont icon-_shebeibaoyang"></i>
					<div>保养查看</div>
				</router-link>      
			</section>
		    </footer>
		  </footer>
</div>
</template>
<script>
	import {enginnerOrder} from '../../../axiosConfig/axiosUtil'
  export default {
    name: 'Alreadyorder',
    data () {
    return {
      items:'',
      showgd:false,
      dd: '',
			formV: {
				woStatus: '2'
			}
    }
  },
	mounted(){
		sessionStorage.removeItem('alreadyorderV')
	},
  methods:{
		getData(){
			var that = this
			enginnerOrder(that.formV)
			.then((res) => {
			if(res.data.data==''){
	            this.showgd=true;
	          }
				if(res.data.code == 0){
					for(var i = 0,l = res.data.data.length;i<l;i++){
						res.data.data[i].repairTime = that.$date(res.data.data[i].repairTime)
			if(res.data.data[i].faultType == '事故类'){
              res.data.data[i].dd = '故障描述'
              // that.dd == '故障描述'
            }else{
              // alert(2)
              res.data.data[i].dd = '需求描述'
              // that.dd == '需求描述'
            }
						for(var v in res.data.data[i]){
							if(!res.data.data[i][v]){
								res.data.data[i][v] = '暂无'
							}
						}
					}
					that.items = res.data.data					
				}
			})
		},
		details(str){
			var that = this
			sessionStorage.setItem('alreadyorderV',JSON.stringify(str))	
			// console.log(str);
			if(str.woState == '已到达现场'){	
				if(str.faultType == '事故类'){				
					that.$router.push({path:'/engineer/sceneSolve02'})
				}else{
					that.$router.push({path:'/engineer/demandScan'})
				}
			}
			else if(str.woState == '电话已解决，请填写反馈'){		
				if(str.faultType == '事故类'){
					that.$router.push({path:'/engineer/sceneSolveFeedback'})
				}else{
					that.$router.push({path:'/engineer/demandFeedback'})
				}				
			}else if(str.woState == '电话未解决，请到达现场'){
				// alert(2)
				if(str.faultType == '事故类'){					
					that.$router.push({path:'/engineer/closeOrder02'})
				}else{				
					that.$router.push({path:'/engineer/demandScan'})
				}
			}else if(str.woState == '已到达现场，未反馈'){
				// alert(1)
				that.$router.push({path:'/engineer/demandFeedback'})				
			}else if(str.faultType != '事故类'){
				that.$router.push({path: "/engineer/demand"})
			}else{
				that.$router.push({path: "/engineer/closeOrder02"})				
			}
		}
  }, 
  created(){
		this.getData()
  }


}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>
