            <img src="static/img/som1.png">
          </div>
        </div>
        <div class="layui-colla-item">
          <h2 class="layui-colla-title">2、手机端故障报修及服务需求提出</h2>
          <div class="layui-colla-content">
            <p>登录后的页面，进入如下图示，就故障报修及服务需求，提供三种渠道：</br>1)&nbsp;扫一扫报修------针对事故类（如硬件故障），且方便扫码自动获取设备信息的情况。扫码后需要点选事故类型（软件故障、硬件故障、耗材断供）。然后进一步选取故障描述，当选取其他时，需手工录入故障内容，并可添加照片。</p>
            <img src="static/img/som38.png">
            <img src="static/img/som39.png">
            <img src="static/img/som40.png">
            <p>2)&nbsp;手动报修------针对事故类（硬件故障），但不方便扫码的情况下，可以点击手动报修，进入手动填写报修信息页面。硬件故障的故障。</p>
            <img src="static/img/som29.png">
            <p>3)&nbsp;服务需求------针对需求类的服务，如：培训、设备变动、安装实施、耗材补充等。</p>
            <img src="static/img/som30.png">
            <img src="static/img/som31.png">
            <img src="static/img/som32.png">
          </div>
        </div>
        <div class="layui-colla-item">
          <h2 class="layui-colla-title">3、客户手机端短信通知</h2>
          <div class="layui-colla-content">
            <p>客户在提交报修后，迅维公司客服在PC端可以查看客户报修及服务需求，确认需求和报修后，会在SOM系统内派出工单，与此同时，该服务需求派出工单时，会同时发送短信通知客户，该报修及需求已受理并派单。在需求处理及维修完成后，同样会发送一条短信给客户，告知服务完成，及提醒客户确认服务结果，并就该次服务给与评价。</p>
            <img src="static/img/som33.png">
            <img src="static/img/som34.png">
          </div>
        </div>
        <div class="layui-colla-item">
          <h2 class="layui-colla-title">4、手机端服务工单及状态查看</h2>
          <div class="layui-colla-content">
            <p>客户可以点击“报修记录”，来进入查看工单，并且可以进一步点击“工单详情”查看详情。</p>
            <img src="static/img/som35.png">
          </div>
        </div>
        <div class="layui-colla-item">
          <h2 class="layui-colla-title">5、手机端服务确认及满意度评价</h2>
          <div class="layui-colla-content">
            <p>当服务完成时，SOM系统会发送提醒短信，告知客户服务完成，并提醒客户就服务进行评价。客户可以点击“报修记录”，并在“已完成”的工单，查看进度，并且可以进一步点击“工单详情”查看详情，接下来可以在右上角的“评价”进入评价界面，就本次服务进行满意度评价和反馈。</p>
            <img src="static/img/som36.png">
            <img src="static/img/som37.png">
          </div>
        </div>
