<template>
  <div>
	<div class="top"></div>

          <div class="confirm iconfont icon-tishi- display-none"> 注意: 带"*"的填写项,何美/HR机器必填</div>

     <div class="nav-fixed">
          <div class="nav">
          	<a @click="$router.back(-1)" class="iconfont icon-fanhui"></a>
          		<div>维修反馈</div>
          		<a></a> 
          </div>
     </div>
     <!--  详情列表 -->
    <div class="feedback-box" style="margin-top:1.25rem">
		<ul>
			<li><span><em class="red">* </em>故障现象分类:</span><span @click='isShowa=false' class="select" id="select" style="width: 60%"><span class="select-val">{{ formV.faultClass }}</span><a>▼</a></span></li>
			<li><span><em class="red" style="visibility: hidden;">* </em>故障分类代码:</span><span id="faulttext">{{ formV.faultNo }}</span></li>
		</ul>
	</div>
				
	<ul class="feedback-box" style="margin-top:1.25rem">
		<li><span><em class="red">*</em> 黑白读数:</span><a><input type="" name="" v-model='formV.bwReader' style='border:1px solid #ddd;width:100%'></a></li>
		<li><span><em class="red">*</em> 彩色读数:</span><a><input type="" name="" v-model='formV.coReader' style='border:1px solid #ddd;width:100%'></a></li>
	</ul>

          <div class="feedback-box" style="margin-top:1.25rem">
          <h1>故障现象描述</h1>
          <textarea placeholder="请输入故障现象描述" maxlength="500" v-model="formV.maintenanceFeedback"></textarea>
           <h1>处理措施</h1>
          <textarea placeholder="请输入处理措施" maxlength="500" v-model="formV.treatmentMeasure"></textarea>
         </div>
				
				
         <div class="feedback" :class="{'display-none': isShowa}">
               <div class="feedback-1">
               <ul v-for='(va,index) in faultClassV'>
                    <label><li @click='ac(index)'><label>{{ va }}</label><input type="radio" name="feedback"></li></label>                   
               </ul>
               </div>
          </div>

     <!--      <div class="feedback01 display-none">
               <div class="feedback-2">
               <ul>
                    <li><label>B01-白点</label><input type="radio" name="feedback" checked='ture'></li>
                    <li><label>B02-图像区域空白</label><input type="radio" name="feedback"></li>
                    <li><label>B03-垂直白条(CD方向)</label><input type="radio" name="feedback"></li>
                    <li><label>B04-水平白条(FD方向)</label><input type="radio" name="feedback"></li>
                     <li><label>B09-其他图像空白</label><input type="radio" name="feedback"></li>
               </ul>
               </div>
          </div> -->
          <div class="footer-none"></div>
          <div class="bottom">
               <div v-on:click="completeOrder" class="complete-btn">服务完成</div>
          </div>

</div>
</template>
<script>
import{overOrderAccident,overOrderOther,faultClass} from '../../../axiosConfig/axiosUtil'
export default {
	name: 'closeOrders02',   

	data () {
		return {
			faultClassV: ['图像质量空白','图像质量问题','图像位置不正确','图像再现故障(浓度相关)','图像再现故障(浓度无关)','图像异常','其他的图像质量不良','纸张传输/处理故障','原稿传输故障','异常噪音/气味/外观','控制器控制故障(图像打印控制器/传真等)','主机控制故障','软件故障','机器/产品物理损坏/包装错误','胶装器故障','其他'],
				faultNoV: ['R01~R99','Z01','K01~K99','N01~N99','D01~D99','G01~G99','E01~E99','H01~H99','B01~B99','C01~C99','P01~P03','Q01~Q03','T01~T04','L01~L99','U01~U99','S01~S99'],
				isShowa: true,
				formV: {
					faultClass: '',
					faultNo: '',
					treatmentMeasure: '',
					maintenanceFeedback: '',
					bwReader: '',
					coReader: ''					
				},
				items: ''
			}
		},
     methods:{
		 getData(){
			var that = this
			that.items = JSON.parse(sessionStorage.getItem('alreadyorderV'))
		 },
      // 服务完成
		 completeOrder() {
			var that =this
			that.formV.woNumber = that.items.woNumber
			// console.log(that.formV)
			overOrderAccident(that.formV)
			.then((res) => {
				if(res.data.code == 0){
					that.$layerMsg(res.data.msg)
					that.$router.push({path:'/engineer/completeOrder'})
				}else{
					that.$layerMsg(res.data.msg)
				}
			})
			 
		     
		}, 
		ac(str){
			var that = this
			that.formV.faultClass = that.faultClassV[str]
			that.formV.faultNo = that.faultNoV[str]
// 			faultClass(that.formV)
// 			.then((res) => {
// 				if(res.data.code == 0){
// 					that.formV.faultNo = res.data.faultNo
// 				}
// 			})
			that.isShowa = true
		}
  }, 
   created(){   
	   this.getData()
  },


}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



