<template>
  <div>
     <div class="nav-fixed">
          <div class="nav">
             工单
          </div>
     </div>
     <div class="top"></div>


<!-- 工单 -->

			<div class="receipt">
				<div class="" id="etc"><router-link style='width:100%' to='/engineer/alreadyorder'>已接单</router-link></div>
				<div class="" id=""><router-link style='width:100%' to='/engineer/examineOrder'>待订件</router-link></div>
				<div class="receipt-color" id=""><router-link style='width:100%; color: #7598ff' to='/engineer/completeOrder'>已完成</router-link></div>
				<div class="" id=""><router-link style='width:100%' to='/engineer/catchSheet'>已转单</router-link></div>
			</div>

            <div class="demand-box" id="completion-type">
							<a @click='details(list)' v-for='list in items'>
								<!-- {{ formV.woNumber }} -->
								<div class="demand-time">
								     <span>{{ list.repairTime }}</span>
								</div>
								<div class="demand-box1">
								   <div class="demand-box2">
								     <h1 class="iconfont icon-shebei1"><a>{{ list.woNumber }}</a></h1>
								     <div :class="list.woState"></div>  
								   </div>
								     <h2 class="demand-name">客户名称:<span>{{ list.custName }}</span></h2>
								      <h2 class="demand-name-1">服务类型:<span>{{ list.faultType }}</span></h2>
								     <div class="demand-box3">
								     <h1 class="demand-name">{{ list.dd }}:<span>{{ list.describe }}</span></h1>
								     <a>查看详情>></a>
								     </div>
								</div>
							</a>               
            </div>
  
            <div v-show='showgd' style="text-align: center; font-size: 2.5rem;padding: 1.5rem 0;color: #666666">暂无工单！</div>
		<!-- 底部 -->
<div class="footer-none"></div>
  		  <footer class="footer1">
  		    <footer class="footer">
  		      <section  class="on">
  				<router-link class='this' :to='{path: "/engineer/index"}'>
  					<i class="iconfont icon-shouye"></i>
  					<div>首页</div>
  				</router-link>      
  		      </section>
  			<section>
  				<router-link class='this' :to='{path: "/engineer/alreadyorder"}'>
  					<i  style='color:#7898ff' class="iconfont icon-gongdan"></i>
  					<div style='color:#7898ff'>工单</div>				
  				</router-link>
  			</section>      
  			<section>
  				<router-link class='this' :to='{path: "/engineer/index03"}'>
  					<i class="iconfont icon-_shebeibaoyang"></i>
  					<div>保养查看</div>
  				</router-link>      
  			</section>
  		    </footer>
  		  </footer>

</div>
</template>

<script type="text/javascript">
	import {enginnerOrder} from '../../../axiosConfig/axiosUtil'
	export default {
		name: 'ExamineOrder',  
		data () {
			return {
				items:'',
        showgd:false ,
        dd: '',
				formV: {
					woStatus: '4'
				}
			}
		},
		mounted(){
			sessionStorage.removeItem('completeOrderV')
		},
		methods:{  
			getData(){
				var that = this
				enginnerOrder(that.formV)
				.then((res) => {
          if(res.data.data==''){
            this.showgd=true;
          }
					if(res.data.code == 0){
						for(var i = 0,l = res.data.data.length;i<l;i++){
							res.data.data[i].repairTime = that.$date(res.data.data[i].repairTime)
              if(res.data.data[i].faultType == '事故类'){
              res.data.data[i].dd = '故障描述'
              // that.dd == '故障描述'
            }else{
              // alert(2)
              res.data.data[i].dd = '需求描述'
              // that.dd == '需求描述'
            }
							for(var v in res.data.data[i]){
								if(!res.data.data[i][v]){
									res.data.data[i][v] = '暂无'
								}
							}
						}
						that.items = res.data.data				
					}
				})
			},
			details(str){
				var that = this
				sessionStorage.setItem('completeOrderV',JSON.stringify(str))
				// console.log(str);
        if(str.faultType=='事故类'){
          that.$router.push({path: "/engineer/repairStatus"})
        }else{
          that.$router.push({path: "/engineer/repairStatusto"})
        }
				
			}
		}, 
		created(){     
			this.getData()
		},
	
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>
</script>
