<template>
  <div>

     <div class="nav-fixed">
          <div class="nav">
             报修记录
          </div>
     </div>
     <div class="top"></div>


<!-- 工单 -->

			<div class="receipt">
				<div class="receipt-color" v-on:click="repairIndexComplete">已完成</div>
				<div v-on:click="repairindexno">未完成</div>
        <div v-on:click="repairIndex" >全部</div>
			</div>

        <!--   待受理 -->
    <a v-for="list in items" @click='details(list)'>
			<div  class="receipt-index-box">
      <div class="receipt-index-box1">
                    <div class="receipt-index-box2">
        <h2 class="iconfont icon-shebei1"><a>{{ list.woNumber }}</a></h2>
                      <a>查看详情>></a>
                    </div>
         <div class="repair-details">
          <ul>
                   <li><span>设备名称:</span><a>{{ list.devName }}</a></li> 
                   <li><span>设备型号:</span><a>{{ list.unitType }}</a></li> 
                   <li><span>设备位置:</span><a>{{ list.location }}</a></li>
                   <li><span>客户名称:</span><a>{{ list.custName }}</a></li>
                   <li><span>部门:</span><a>{{ list.department }}</a></li>
              </ul>
         </div>    
                <div class="repair-state">
                  <span><i class="iconfont icon-shijian1"></i> <p>{{ list.repairTime }}</p></span>
                  <span><i class="iconfont icon-zhuangtai "></i>
                  <p class="details-text">{{ list.woState }}</p></span>
                </div>   
      </div>
      </div>
    </a>

     <a v-for="list2 in itemsB" @click='detailsB(list2)'>
      <div  class="receipt-index-box">
      <div class="receipt-index-box1">
                    <div class="receipt-index-box2">
        <h2 class="iconfont icon-shebei1"><a>{{ list2.woNumber }}</a></h2>
                      <a>查看详情>></a>
                    </div>
         <div class="repair-details">
          <ul>
                   <li><span>联系人:</span><a>{{ list2.repairMan }}</a></li> 
                   <li><span>联系号码:</span><a>{{ list2.repairPhone }}</a></li> 
                   <li><span>服务类型:</span><a>{{ list2.faultType }}</a></li>
                   <li><span>服务类别:</span><a>{{ list2.accidentType }}</a></li>
                   <li><span>需求描述:</span><a>{{ list2.describe }}</a></li>
              </ul>
         </div>    
                <div class="repair-state">
                  <span><i class="iconfont icon-shijian1"></i> <p>{{ list2.repairTime }}</p></span>
                  <span><i class="iconfont icon-zhuangtai "></i>
                  <p class="details-text">{{ list2.woState }}</p></span>
                </div>   
      </div>
      </div>
    </a>
		<div v-show='showgd' style="text-align: center; font-size: 2.5rem;padding: 1.5rem 0;color: #666666">暂无工单！</div>


       
		<!-- 底部 -->
       <div class="footer-none"></div>
	<footer class="footer1">
		<footer class="footer">
			<section v-on:click="index">
			<i class="iconfont icon-shouye"></i>
			<div>首页</div>
			</section>
			<section class="on" v-on:click="repairIndex">
			<i class="iconfont icon-baoxiu"></i>
			<div>报修记录</div>
			</section>
			<section v-on:click="feedbackindex">
			<i class="iconfont icon-yijianfankui"></i>
			<div>意见反馈</div>
			</section>
		</footer>
	</footer>


</div>
</template>
<script type="text/javascript">

import {customerWeChatOrder} from '../../../axiosConfig/axiosUtil'
    export default {
      name: 'RepairIndexComplete',   
    
      data () {
    return {
      formV: {
        woStatus: 1,
        machCode: '',
        devName: '',
        unitType: '',
        department: '',
        repairMan: '',
        repairPhone: '',
        location: '',
        faultType: '',
        accidentType: '',
        faultNo: '',
        describe: '',
        repairTime: '',
        serviceArea: '',
        custName: '',
        woNumber: '',
        woState: '',
        turnOrderReson: '',
        enginnerName: '',
        enginnerPhone: '',
      },
    items: [],
     itemsB: [],
		showgd:false, 
    }
		
  },
    mounted(){
    sessionStorage.removeItem('repairIndexV')
  },
   methods:{
    getData(){ 
      var that = this;
      customerWeChatOrder(that.formV)
      .then((res) => {
				if(res.data.data==''){
				    this.showgd=true;
				  }
        if(res.data.code == 0){
          // res.data.data.repairTime = that.$date(res.data.data.repairTime)
          for(var i = 0,l = res.data.data.length;i<l;i++){
            res.data.data[i].repairTime = that.$date(res.data.data[i].repairTime)
            for(var v in res.data.data[i]){
              if(!res.data.data[i][v]){
                res.data.data[i][v] = '暂无'
              }
            } 
            if(res.data.data[i].faultType == '事故类'){
              that.items.push(res.data.data[i])
            }
            // else if(res.data.data[i].faultType != '事故类'){
            //   that.itemsB.push(res.data.data[i])
            // }
          }
         
        }
      })
    },
  getDataB(){ 
      var that = this
      customerWeChatOrder(that.formV)
      .then((res) => {
        if(res.data.code == 0){
          // res.data.data.repairTime = that.$date(res.data.data.repairTime)
          for(var i = 0,l = res.data.data.length;i<l;i++){
            res.data.data[i].repairTime = that.$date(res.data.data[i].repairTime)
            for(var v in res.data.data[i]){
              if(!res.data.data[i][v]){
                res.data.data[i][v] = '暂无'
              }
            }
            if(res.data.data[i].faultType != '事故类'){
              that.itemsB.push(res.data.data[i])
            }
            sessionStorage.setItem(2,that.itemsB)
          }
          
        }
      })
    },
    details(str){
      var that = this
      sessionStorage.setItem('repairIndexV',JSON.stringify(str))
     var statevalue = str.woState
      that.$router.push({path:'/customer/repairStatus',query:{name:statevalue}}) 
    },

       detailsB(str){
      var that = this
      sessionStorage.setItem('repairIndexV',JSON.stringify(str))
     var statevalue = str.woState
      that.$router.push({path:'/customer/repairStatusTo',query:{name:statevalue}}) 
    },
    repairIndex: function () {
      this.$router.push({path:'/customer/repairIndex'})
    },
   index: function () {
      this.$router.push({path:'/customer/index'})
    },
    feedbackindex:function(){
      this.$router.push({path:'/customer/feedbackindex'})
    },
    repairindexno:function(){
      this.$router.push({path:'/customer/repairindexno'})
    },
    repairIndex:function(){
      this.$router.push({path:'/customer/repairIndex'})
    },
    repairIndexComplete:function(){
      this.$router.push({path:'/customer/repairIndexComplete'})
    },
     state: function (e) {  
    var statevalue=e.currentTarget.innerText;
    this.$router.push({path:'/customer/repairStatus',query:{name:statevalue}}) 
    },


  }, 
   created(){
            this.getDataB();
              this.getData();
      $(function(){ 
      


      })

  },


}

</script>


<style scoped>

</style>



