<template>
  <div>
  
	<div class="top-order"></div>
     <div class="nav-fixed">
          <div class="nav">
          	<a @click='$router.back(-1)' class="iconfont icon-fanhui"></a>
          		<div>工单详情</div>
          		<a></a> 
          </div>
     </div>

     <div class="details-table">
     	<div class="details-table-title">
     		<div class="company">{{ formV.custName }}</div>
     		<div class="job-number">工单号:<span>{{ formV.woNumber }}</span></div>

     	</div>
     	<div class="details-table-box">
     		<ul>					
					<li><span>机器编码:</span><a>{{ formV.machCode }}</a></li> 
					<li><span>设备名称:</span><a>{{ formV.devName }}</a></li> 
					<li><span>设备型号:</span><a>{{ formV.unitType }}</a></li>
					<li><span>设备位置:</span><a>{{ formV.location }}</a></li>
					<li><span>客户名称:</span><a>{{ formV.custName }}</a></li>
					<li><span>部门:</span><a>{{ formV.department }}</a></li>
					<li><span>联系人:</span><a>{{ formV.repairMan }}</a></li>
					<li><span>联系号码:</span><a>{{ formV.repairPhone }}</a></li>
					<li><span>服务类型:</span><a>{{ formV.faultType }}</a></li>
					<li><span>服务类别:</span><a>{{ formV.accidentType }}</a></li>
					<li><span>需求描述:</span><a>{{ formV.describe }}</a></li>
					<li><span>提出时间</span><span>{{formV.repairTime}}</span></li>
					<li><span>工程师:</span><a>{{ formV.enginnerName }}</a></li>
					<li><span>工程师电话:</span><a>{{ formV.enginnerPhone }}</a></li>
					<li><span>故障照片</span>
						<span class="faultImg">
							{{showNull}}
							<img v-for="(item,index) in picture" :key="index" :src="item.custName" @click="pictureBig(index)">
						</span>
					</li>
					<div class="order-img">
					</div>
      	</ul>
     	</div>
     </div>
       <div class="completa-list">    
       <div class="decice-for-box">
               <span>零件订购清单</span>
                <span class="decice-green">{{ formV.woState }}</span>
             </div>  
          <ul v-for="(va,index) in formV.partMessage">
               <li><span>零件{{ index+1 }} (型号 数量) :</span><a>{{ va }} 件</a></li>
               <!-- <li><span>零件2 (型号 数量) :</span><a>BBB 5件</a></li> -->
          </ul>
     </div>

 </div>
</template>

<script>
import {woNumberPicture} from '../../../axiosConfig/axiosUtil'	
export default {
	name: 'AdoptOrder',   

	data () {
	return {
		isShow:false,
		// formV:'',
		picture: '',
		isShade: false,
		pictureB: '',
		showNull: '',
	}
  },
	methods:{
		toggle:function(){
			this.isShow = !this.isShow;
		},
		getData(){
		  var that = this
		  that.formV = JSON.parse(sessionStorage.getItem('examineOrderV'))
		//   console.log(that.formV)
		},
		pictureBig(inx){
			var that = this
			// console.log(inx)
			// console.log(that.picture)
			
			that.isShade = true
			that.pictureB = that.picture[inx].custName
			// console.log(that.pictureB,"1111")
		},
		picShade(){
			this.isShade = false 
		}

  }, 
	created(){
		this.getData()
		let picFault = {woNumber:this.formV.woNumber}
		woNumberPicture(picFault).then((res) => {
				if(res.data.msg == '对不起，该工单暂无图片'){
					this.showNull = '暂无图片'
				}else{
					this.picture = res.data.data
				}
			})
  },


}

</script>


<style scoped>

</style>



